            <select className="form-select" id="SERIESTIPODOCUMENTO"  aria-label="Default select example">
            <option value="">SELECCIONE</option>
            <option value="1">PENDIENTE</option>
            <option value="2">RECIBIDA</option>
            <option value="3">COSTEADA</option>
            </select>
