import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types'
import Constantes from '../Constante';
import Paginate from '../Paginacion/Paginate';
import Loading from '../Loading';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useHistory
  } from "react-router-dom";
function PeticionNoEnviada(props) {
    const [enviospedidos, setEnvios]            =   useState([]);
    const [actualizar, setActualizar]           =   useState(0);
    const [lodingg, setLodingg]                 =   useState(false)
    const [tablacompra, setTablaCompras]        =   useState([]);
    const [busqueda, setBuscar]                 =   useState([]);
    const SELECTENVASE = () =>
    {
        setLodingg(true);
        fetch(`${Constantes.RUTA_API8}/obtener-peticionesnoEnviadas.php`).then((res) =>
        res.json()).then((data)=>{
        setEnvios(data);
        setTablaCompras(data);
        //setTabla(data);
        //console.log(data);
        setLodingg(false);
    });    
    }
//:::::::::::::::::::::::::::::::::  FUNCION PARA BUSCADOR ::::::::::::::::::::::::::::::::::::::::::::::::::::::
const BuscarCOMPRA2 = (e) =>
{
    let valor           = document.getElementById("BuscarCOMPRASTABLANo").value;
    e.preventDefault();
    var codigo          = e.which || e.keyCode;
    if (codigo == 127){
    }
    if(codigo >= 32 && codigo <= 126)
    {
        const bandera   =   1;  
        //console.log(String.fromCharCode(codigo));
        valor           = valor + String.fromCharCode(codigo);
    }
      document.getElementById("BuscarCOMPRASTABLANo").value = valor
      filtrar(document.getElementById("BuscarCOMPRASTABLANo").value);
}
//:::::::::::::::::::::::::::::::FUNCION FILTRAR::::::::::::::::::::::::::::::::::::::::::::::::::::::
const filtrar = (busqueda) => 
{
    const resultadoBusqueda = tablacompra.filter((elementos) =>
    {
        if(elementos.id.toString().includes(busqueda)||elementos.nombreunidad.toLowerCase().toString().includes(busqueda))
        {
            return elementos;
        }
    });
    setBuscar(resultadoBusqueda);
    setEnvios(resultadoBusqueda);
}
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
    useEffect(() => {
        SELECTENVASE();
    }, [actualizar])
    return (
        <>
            <div id="" className="row  tipoletra">
            <div className="">
        <div id="" className="row ">
            <div className="col">
                <button className="btn btn-primary" onClick={()=>setActualizar(actualizar+1)} title="Actualizar Tabla" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-clockwise" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                </svg>
                </button>
            </div>
            <div className="col-sm-6">
                <div className="input-group mb-3 mb-6 tamañodelbuscador ">    
                <input type="text"  onKeyPress={BuscarCOMPRA2} className="form-control" id="BuscarCOMPRASTABLANo" aria-describedby="basic-addon2"   placeholder="Digite para buscar" title="Digitar para buscar..." />
                <span className="input-group-text" id="basic-addon2" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-keyboard" viewBox="0 0 16 16">
                <path d="M14 5a1 1 0 0 1 1 1v5a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V6a1 1 0 0 1 1-1h12zM2 4a2 2 0 0 0-2 2v5a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V6a2 2 0 0 0-2-2H2z"/>
                <path d="M13 10.25a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm0-2a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-5 0A.25.25 0 0 1 8.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 8 8.75v-.5zm2 0a.25.25 0 0 1 .25-.25h1.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-1.5a.25.25 0 0 1-.25-.25v-.5zm1 2a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-5-2A.25.25 0 0 1 6.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 6 8.75v-.5zm-2 0A.25.25 0 0 1 4.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 4 8.75v-.5zm-2 0A.25.25 0 0 1 2.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 2 8.75v-.5zm11-2a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-2 0a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-2 0A.25.25 0 0 1 9.25 6h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 9 6.75v-.5zm-2 0A.25.25 0 0 1 7.25 6h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 7 6.75v-.5zm-2 0A.25.25 0 0 1 5.25 6h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 5 6.75v-.5zm-3 0A.25.25 0 0 1 2.25 6h1.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-1.5A.25.25 0 0 1 2 6.75v-.5zm0 4a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm2 0a.25.25 0 0 1 .25-.25h5.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-5.5a.25.25 0 0 1-.25-.25v-.5z"/>
                </svg>
                </span>
                </div>
            </div>   
        </div>  
            <table className="table table-hover table-bordered  table-responsive-md ">
            <thead className='tipoletra'>
            <tr>
                <th scope="col">#</th>
                <th scope="col"><span className="badge bg-light text-dark"> 
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-send-plus" viewBox="0 0 16 16">
                <path d="M15.964.686a.5.5 0 0 0-.65-.65L.767 5.855a.75.75 0 0 0-.124 1.329l4.995 3.178 1.531 2.406a.5.5 0 0 0 .844-.536L6.637 10.07l7.494-7.494-1.895 4.738a.5.5 0 1 0 .928.372l2.8-7Zm-2.54 1.183L5.93 9.363 1.591 6.602l11.833-4.733Z"/>
                <path d="M16 12.5a3.5 3.5 0 1 1-7 0 3.5 3.5 0 0 1 7 0Zm-3.5-2a.5.5 0 0 0-.5.5v1h-1a.5.5 0 0 0 0 1h1v1a.5.5 0 0 0 1 0v-1h1a.5.5 0 0 0 0-1h-1v-1a.5.5 0 0 0-.5-.5Z"/>
                </svg> UNIDAD OPERATIVA</span></th>
                <th scope="col">FECHA DE PEDIDO</th>
                <th scope="col">ESTADO</th>
                <th scope="col">ACEPTAR PEDIDO</th>
               
                {/*<th scope="col">Eliminar</th>*/}
            </tr>
            </thead>
            <tbody>
            {lodingg && <Loading/> }
            {enviospedidos.map(data_enviodetalle =>{
                return(
                    <tr>
                        <td>{data_enviodetalle.id}</td>
                        <td>{data_enviodetalle.nombreunidad}</td>
                        <td>{data_enviodetalle.fh_recepcion_pedido}</td>
                        <td>ACEPTADA</td>
                        <td ><Link to={`/InformacionDeEnvios/${data_enviodetalle.id}`}  className="btn colorbotonesver centrandoicono " >
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-calendar4-week" viewBox="0 0 16 16">
                        <path d="M3.5 0a.5.5 0 0 1 .5.5V1h8V.5a.5.5 0 0 1 1 0V1h1a2 2 0 0 1 2 2v11a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V3a2 2 0 0 1 2-2h1V.5a.5.5 0 0 1 .5-.5zM2 2a1 1 0 0 0-1 1v1h14V3a1 1 0 0 0-1-1H2zm13 3H1v9a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V5z"/>
                        <path d="M11 7.5a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm-3 0a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm-2 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm-3 0a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1z"/>
                        </svg>
                        </Link>
                        </td> 
                    </tr>
                );
            })}
            </tbody>
            </table>
    </div>  
    </div>
        </>
    )
}

PeticionNoEnviada.propTypes = {

}

export default PeticionNoEnviada

