import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types'
import Constantes from '../Constante';
import Navegacion from '../paginainicial/Navegador';
import PiedePagina from '../paginainicial/Piedepagina';
import Paginate from '../Paginacion/Paginate';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useHistory,
    useParams
  } from "react-router-dom";
function AgregarProductoExtra(props) {
    const[actualizador, SETACTUALIZADOR] = useState(0)
    const {id} = useParams();
    const history = useHistory();
    const menuinicial = () =>
    {
      history.push("/Envio_Productos")
    }
// ::::::::::::::::::::::::  OBTIENE ENVIO RECEPCION ::::::::::::::::::::::::::::::::::::
const [informacionenvio, setEnvios_info]           =   useState([]);
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
const selecEnvioDetalle = () =>
{
   fetch(`${Constantes.RUTA_API8}/obtener-enviodetalle_info.php`, {
    method: "POST",
    body: id, 
    })
  .then((res) =>res.json())
  .then((data)=>{
    setEnvios_info(data);
  });
}
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                           EXTRAYENDO TODOS LOS PRODUCTOS     
const [todoslosproductos, setTodosLosProductos] =   useState([]);
const SELECT_TODOSLOSPRODUCTOS = () =>
{
    fetch(`${Constantes.RUTA_API8}/obtener-todoslosproductos.php`).then((res) =>
    res.json()).then((data)=>{
    setTodosLosProductos(data);
    });    
}
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                   EXTRAYENDO PRESENTACIONES DEL PRODUCTO SELECCIONADO 
const[presentacionSelecionada, spresentacionSelecionada] = useState([])
const FUNCION_PROVEEDORES_SELECCIONADOS= () => 
{  
    const idS = document.getElementById("PRODUCTOSELCCION").value;//TOMO EL VALOR ID DEL PRODUCTO
    fetch(`${Constantes.RUTA_API6}/obtener-presentaciondeproductoid.php`, { //CREO UNA PROMESA PARA BUSCAR EN EL SERVIDOR EL PRODUCTO CON ESE ID
        method: "POST",
        body: idS, 
        })
    .then((res) =>res.json())
    .then((data)=>{
        spresentacionSelecionada(data);
    });
    const fechaactual           =   new Date();
    const estructuraFecha       =   fechaactual.getFullYear()+"-"+(fechaactual.getMonth()+1)+"-"+fechaactual.getDate()+" "+fechaactual.getHours()+":"+fechaactual.getMinutes()+":"+fechaactual.getSeconds();;
    document.getElementById("idfh_agregar").value = estructuraFecha;
 }
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//               GUARDANDO PRDUCTO EXTRA AL PEDIDO 

const ENVIANDO_DATOS = (e) => {
    e.preventDefault();
    if(e.target[1].value == ""  || e.target[3].value == ""  || e.target[3].value <= 0 || e.target[4].value == "")
    {
        toast.error('¡¡ ERROR !! INGRESAR DATOS NECESARIOS ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            });
    }
    else
    {
        const objeto_envios_detalle =
        {
        envios_id                   :   id,
        presentacion_producto_id    :    e.target[1].value,
        cantidad_pedida             :    e.target[3].value,
        fh_creacion                 :    e.target[4].value,
        }
         const pasando_objetoJSON = JSON.stringify(objeto_envios_detalle);
        // console.log("SOY CARGA UTIL"+ pasando_objetoJSON);
        fetch(`${Constantes.RUTA_API8}/guardar_envio_detalle2.php`, {
            method: "POST",
            body:pasando_objetoJSON, 
            });
        toast.success('PETICIÓN REGISTRADA CON EXITO ', {
                position: "top-left",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
        });
            SETACTUALIZADOR(actualizador+1);
        
    }

}
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::  
useEffect(() => {
    SELECT_TODOSLOSPRODUCTOS();
    selecEnvioDetalle();
    //console.log(actualizador);
    fetch(`${Constantes.RUTA_API8}/obtener-enviodetalle_info.php`, {
        method: "POST",
        body: id, 
        })
      .then((res) =>res.json())
      .then((data)=>{
        setEnvios_info(data);
      });
}, [actualizador])
    return (
        <>
        <Navegacion/>
        <div className="container tipoletra">
        <ToastContainer/>
        <div className="row">
        <div className="col">
                <button onClick={menuinicial}type="button" className="btn colormenutablas btn-sm espaciomenusuperior">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-left-square-fill" viewBox="0 0 16 16">
                <path d="M16 14a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v12zm-4.5-6.5H5.707l2.147-2.146a.5.5 0 1 0-.708-.708l-3 3a.5.5 0 0 0 0 .708l3 3a.5.5 0 0 0 .708-.708L5.707 8.5H11.5a.5.5 0 0 0 0-1z"/>
                </svg> REGRESAR A ENVIOS
                </button>
                <br/><br/>
        </div>
        
        </div>
        <div id="RPRODUCTO" className="row ">
            
            <div id=""  className="col-sm-3 tipoletra colorfondobarra">
            <div className='row tipoletra'>
                <form onSubmit={ENVIANDO_DATOS}>
                <div className="w-100">
                    <div className="form-group">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-exclamation-circle-fill coloricono" viewBox="0 0 16 16">
                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4zm.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2z"/>  
                    </svg>
                    <label for="exampleFormControlSelect1" className='tipoletra2'> SELECCIONAR PRODUCTO   
                    </label>
                    <select className="form-control form-control-sm" id="PRODUCTOSELCCION" required onClick={FUNCION_PROVEEDORES_SELECCIONADOS}  aria-label="Default select example">
                     {todoslosproductos.map(data_productos => {
                        return(
                        <option value={data_productos.id}>{data_productos.nombre}</option>
                     );
                    })}
            </select>
                    </div>
            </div>
            <div className='w-100'>
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-exclamation-circle-fill coloricono" viewBox="0 0 16 16">
                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4zm.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2z"/>  
                </svg>
                <label for="exampleFormControlInput1" className="form-label tipoletra2">SELECCIONE PRESENTACIÓN :  </label>
                <select className="form-select" id="IDPRESENTACION" required aria-label="Default select example">
                    {presentacionSelecionada.map(presentacion_data => {
                        return( <option value={presentacion_data.id}>{presentacion_data.descripcionpresentacion}</option>);
                    })}
                </select>
            </div>
            <div className="w-100">
                <button type="button" className="btn btn-danger btn-sm form-label tipoletra" onClick={FUNCION_PROVEEDORES_SELECCIONADOS} title="ACTUALIZAR SERIE" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-clockwise" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                </svg> ACTUALIZAR  PRESENTACION
                </button>
            </div>
            <div className='w-100'>
                <div className="col">
                    <label for="exampleFormControlInput1" className="form-label tipoletra2">DIGITAR CANTIDAD: </label>
                    <input type="number" step="0.01" min="1" required  className="form-control tipoletra"      placeholder="DIGITAR CANTIDAD" />
                </div>
            </div>
            <div className='w-100'>
                <div className="col">
                    <label for="exampleFormControlInput1" className="form-label tipoletra2">FECHA CREACIÓN: </label>
                    <input type="text" disabled className="form-control tipoletra" id="idfh_agregar"  required   placeholder="FECHA DE CREACION" />
                </div>
            </div>
            <div className='w-100'>
                <br/>
            <button type="submit" className="btn btn-success btn-sm tipoletra" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-send-plus" viewBox="0 0 16 16">
                <path d="M15.964.686a.5.5 0 0 0-.65-.65L.767 5.855a.75.75 0 0 0-.124 1.329l4.995 3.178 1.531 2.406a.5.5 0 0 0 .844-.536L6.637 10.07l7.494-7.494-1.895 4.738a.5.5 0 1 0 .928.372l2.8-7Zm-2.54 1.183L5.93 9.363 1.591 6.602l11.833-4.733Z"/>
                <path d="M16 12.5a3.5 3.5 0 1 1-7 0 3.5 3.5 0 0 1 7 0Zm-3.5-2a.5.5 0 0 0-.5.5v1h-1a.5.5 0 0 0 0 1h1v1a.5.5 0 0 0 1 0v-1h1a.5.5 0 0 0 0-1h-1v-1a.5.5 0 0 0-.5-.5Z"/>
                </svg> AGREGAR PRODUCTO
                </button>
            </div>
            <div className='w-100'>
                
            </div>
                </form>
            
            </div>
            </div>
           
            <div id=""  className="col-sm-9 ">
            <div className="col">
                <button className="btn btn-primary" onClick={()=> SETACTUALIZADOR(actualizador+1)} title="Actualizar Tabla" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-clockwise" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                </svg>
                </button>
            </div>
                <table className="table table-bordered  table table-hover tipoletra " id="TABLEPRODUCTOS">
                <thead>
                <tr>
                    <th scope="col">#</th>
                    <th scope="col">PRODUCTO</th>
                    <th scope="col">PRESENTACIÓN</th>
                    <th scope="col">CANTIDAD</th>
                </tr>
                </thead>
                <tbody>
                {informacionenvio.map(data_enviodetalle =>{
                    return(
                        <tr>
                            <td>{data_enviodetalle.id}</td>
                            <td>{data_enviodetalle.nombreproducto}</td>
                            <td>{data_enviodetalle.descripcion_presentacion}</td>
                            <td>{data_enviodetalle.cantidad_pedida}</td>
                        </tr>
                    );
                })}
                </tbody>
                </table>
            </div>
</div>
        </div>
            <PiedePagina/>
        </>
    )
}

AgregarProductoExtra.propTypes = {

}

export default AgregarProductoExtra

