//::::::::::::::::::: REGISTRANDO ENVASES :::::::::::::::::::::::::::::::::::
const CREANDOENVASE = (e) =>
{
    e.preventDefault();
    const objetoenvioenvase =
    {
        nombre:e.target[0].value,
        habilitado:e.target[1].value,
        fh_creacion:e.target[2].value,
        fh_actualizacion:e.target[3].value
    }
    const cargaUtil = JSON.stringify(objetoenvioenvase);
    //console.log(objetoenvio);
    funcionregistroenvase(cargaUtil);
};
// ::::::: FUNCION DE CARGA PARA ENVASES ::::::::: 
const funcionregistroenvase=async(cargaUtil) => 
{
    const respuesta = await fetch(`${Constantes.RUTA_API}/guardar-envase.php`, 
    {
        method: "POST",
        body: cargaUtil,
    });
    const exitoso = await   respuesta.json();
    if (exitoso) 
    {
        // console.log("entre a notificacion");
        toast.success('ENVASE REGISTRADO ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
        
    }else
    {
        toast.error('NO SE  REGISTRO ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
}
