//:::::::::::::::::::::::::::::::::  FUNCION PARA VALIDAR ::::::::::::::::::::::::::::::::::::::::::::::::::::::
const validarinputnumero = (e) =>
{
    let valor           = document.getElementById("CANTIDADPRE").value;
    e.preventDefault();
    var codigo          = e.which || e.keyCode;
   
    if(codigo >= 48 && codigo <= 57  || codigo == 46)
    {
        
        //console.log(String.fromCharCode(codigo));
        valor           = valor + String.fromCharCode(codigo);
    }
    
      document.getElementById("CANTIDADPRE").value = valor
}
