import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types'
import Constantes from '../Constante';
import Loading from '../Loading';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function CrearSolicitud(props) 
{
//                  INGRESANDO FECHA PARA COMPRAS 
const [contadorefecto, setcontadorefecto] = useState(0);
const [contador, SetContador] = useState(0);
const [lodingg, setLodingg] = useState(false)
var iRowCount = 0;
const MODALNUEVACOMPRAREGISTRAR = () => 
{
    const fechaactual           =   new Date();
    const estructuraFecha       =   fechaactual.getFullYear()+"-"+(fechaactual.getMonth()+1)+"-"+fechaactual.getDate()+" "+fechaactual.getHours()+":"+fechaactual.getMinutes()+":"+fechaactual.getSeconds();;
    document.getElementById("IDFECHACRECION").value = estructuraFecha;
    setcontadorefecto(contadorefecto+1);
}
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                           EXTRAYENDO INFORMACIÓN DE LAS UNIDADES OPERATIVAS     
const [unidadoperativa, setUnidadOperativa] =   useState([]);
const SELECT_UNIDADOPERATIVA = () =>
{
    fetch(`${Constantes.RUTA_API8}/obtener-unidadoperativa.php`).then((res) =>
    res.json()).then((data)=>{
    setUnidadOperativa(data);
    });   
}
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                           EXTRAYENDO TODOS LOS PRODUCTOS     
const [todoslosproductos, setTodosLosProductos] =   useState([]);
const SELECT_TODOSLOSPRODUCTOS = () =>
{
    fetch(`${Constantes.RUTA_API8}/obtener-todoslosproductos.php`).then((res) =>
    res.json()).then((data)=>{
    setTodosLosProductos(data);
    });    
}
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                      AGREGANDO PRODUCTOS PARA ENVIO EN LA TABLA                                        //  
// DECLARACION DE VARIABLES :
    let celda;
    let fila;
    let textoCelda;
    let combo;
    let combo2;
    let selected;
    let selected2;
    let tblBody;
    let tabladeproductos;
//  -------------------------------------------                   
    const AGREGANDO_LISTA_PRODUCTOS = (e) => 
    {
        SetContador(contador+1);
    e.preventDefault();//TOMO EL EVENTO DELBOTON POR EL CUAL PUEDO ACCEDER A LA POSICION DE LOS INPUT
   // SetArreglo(arreglo+1);//GENEREO UN CONTADOR CADA VEZ QUE EL USUARIO INGRESA
    combo               =   document.getElementById("PRODUCTOSELCCION");//REFERENCIA A SELECT DEL PRODUCTO
    selected            =   combo.options[combo.selectedIndex].text;// TOMO SU VALOR DE TEXTO
    combo2              = document.getElementById("IDPRESENTACION");//REFERENCIA A SELECT DE LAS PRESENTACIONES
    selected2           = combo2.options[combo2.selectedIndex].text;//TOMO SU VALOR DE TEXTO
    tabladeproductos    =  document.getElementById("TABLEPRODUCTOS");//REFERENCIA A TABLA 
    tblBody             = document.createElement("tbody");//CREO UN ELEMENTO TBODY PARA LA TABLA
    fila                = document.createElement("tr");//CREO UN ELEMENTO TR PARA LA TABLA
    //------------ CREACION DE LA PRIMERA FILA LOS DATOS QUE TENDRA         //
    // 1      NOMBRE PRODUCTO      PRESENTACION            CANTIDAD  
    celda               = document.createElement("td");//CREO UN ELEMENTO TD PARA LA TABLA
    textoCelda          = document.createTextNode(contador);//AGREGO A LA CELDA EL TEXTO
    celda.id            =  contador;
    celda.appendChild(textoCelda);//ASIGNO A LA CELDA EL TEXTO CREADO DEL ID
    fila.appendChild(celda);//AGREGO EL TD DENTRO DE TR DE LA TABLA
    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(selected);//AGREGO EL TEXTO DEL PRODUCTO SELECCIONADO
    celda.appendChild(textoCelda);//AGREGO EL TEXTO A LA NUEVA CELDA CREADA
    fila.appendChild(celda);//AGREGO A TR LA NUEVA CELDA CREADA CON SU TEXTO
    
    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(e.target[1].value);//AGREGO A LA CELDA EL TEXTO DE LA PRESENTACION
    celda.appendChild(textoCelda);//AGREGO LA CELDA EL TEXTO CREADO
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA


    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(selected2);//AGREGO A LA CELDA EL TEXTO DE LA PRESENTACION
    celda.appendChild(textoCelda);//AGREGO LA CELDA EL TEXTO CREADO
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA
    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(e.target[3].value);//AGREGO EL TEXTO DE LA CANTIDAD
    celda.appendChild(textoCelda);//AGREGO EL TEXTO A LA NUEVA CELDA CREADA
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA 
    tblBody.appendChild(fila);//AGREGO A TBODY EL TR CON SUS CELDAS
    tabladeproductos.appendChild(tblBody); //AGREGO EL TBODY A LA TABLA
}
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                   EXTRAYENDO PRESENTACIONES DEL PRODUCTO SELECCIONADO 
const[presentacionSelecionada, spresentacionSelecionada] = useState([])
const FUNCION_PROVEEDORES_SELECCIONADOS= () => 
{  
    const id = document.getElementById("PRODUCTOSELCCION").value;//TOMO EL VALOR ID DEL PRODUCTO
    fetch(`${Constantes.RUTA_API6}/obtener-presentaciondeproductoid.php`, { //CREO UNA PROMESA PARA BUSCAR EN EL SERVIDOR EL PRODUCTO CON ESE ID
        method: "POST",
        body: id, 
        })
    .then((res) =>res.json())
    .then((data)=>{
        spresentacionSelecionada(data);
    });
 }
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//              OBTENIENDO LOS DATOS DE UNA TABLA 
const[id_envio, SetId_ENVIO] = useState(0)
const ENVIANDO_DATOS = () => 
{
    //INICIALIZACION DE VARIABLES:   
    let unidad_operativo2    =   document.getElementById("unidadoperativaid").value;
    let OBSERVACIONES2       =   document.getElementById("observacionpeticion").value;
    let FECHACREACION2       =   document.getElementById("IDFECHACRECION").value;
    let BODEGA2              =   document.getElementById("IDBODEGAALIJAMIENTO").value;
    //VALIDACION PARA TOMAR EL ID DE LA TABLA 
    if( document.getElementById('TABLEPRODUCTOS').getElementsByTagName('tr') != null)//VERIFICO SI EXISTE MI TABLA
    {
        var oRows           = document.getElementById('TABLEPRODUCTOS').getElementsByTagName('tr');
        iRowCount           = oRows.length-1;
    }
    else
    {
        toast.error('NO EXISTE REGISTRO ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
    if( unidad_operativo2 == "" || FECHACREACION2 == "" || BODEGA2 == "" || iRowCount ==0)//VERIFICO SI LOS CAMPOS REQUERIDOS EXISTEN
    {   //NOTIFICO SI NO EXISTEN LOS DATOS :::::::::::::::::::::::::::::::::::::::::::::::::::
        toast.error('¡¡ ERROR !! INGRESAR DATOS NECESARIOS', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
    else 
    {
        const objeto_envios =
        {
            unidad_operativa        :   unidad_operativo2,
            observacionesdeenvio    :   OBSERVACIONES2,
            fecha_creacion          :   FECHACREACION2,
            bodega                  :  BODEGA2,
        }
        const datos_convertidos = JSON.stringify(objeto_envios);
        //console.log(datos_convertidos);
        oRows = document.getElementById('TABLEPRODUCTOS').getElementsByTagName('tr');
        iRowCount = oRows.length-1;
        //console.log("contador: "+iRowCount);
        fetch(`${Constantes.RUTA_API8}/guardar_envio.php`, {
            method: "POST",
            body:datos_convertidos, 
        })  .then((res) =>res.json())
            .then((data)=>
            {
                //console.log(data);
                SetId_ENVIO(data);
                //console.log(iRowCount)
                var t = document.getElementById("TABLEPRODUCTOS");
                for(let i = 1; i<= (iRowCount); i++)
                {
                        const objeto_envios_detalle =
                        {
                            envios_id                   :   data,
                            presentacion_producto_id    :   t.rows[i].cells[2].innerText,
                            cantidad_pedida             :   t.rows[i].cells[4].innerText,
                            bodega_id_origen            :   BODEGA2,
                            fh_creacion                 :   FECHACREACION2,
                        }
                        const pasando_objetoJSON = JSON.stringify(objeto_envios_detalle);
                       // console.log("SOY CARGA UTIL"+ pasando_objetoJSON);
                        fetch(`${Constantes.RUTA_API8}/guardar_envio_detalle.php`, {
                            method: "POST",
                            body:pasando_objetoJSON, 
                        })
                }
            });  
        toast.success('PETICIÓN REGISTRADA CON EXITO ', {
        position: "top-left",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        });
    }
}
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                   EXTRAYENDO BODEGAS DE UNIDAD OPERATIVA 
const[bodegaunidadOpera, setBODEGAUNIDAD_OPERA] = useState([])
const FUNCION_EXTRAER_BODEGAS= () => 
 {  
   let idunidad = document.getElementById("unidadoperativaid").value;
    // console.log(idunidad);
    fetch(`${Constantes.RUTA_API6}/obtener-bodegaunidadoperativa.php`, {
        method: "POST",
        body: idunidad, 
        })
    .then((res) =>res.json())
    .then((data)=>{
        setBODEGAUNIDAD_OPERA(data);
    });
 }
// :::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
const BORRAR_TABLA = () => 
{   
    if( document.getElementById('TABLEPRODUCTOS').getElementsByTagName('tr') != null)//VERIFICO SI EXISTE MI TABLA
    {
        var oRows           = document.getElementById('TABLEPRODUCTOS').getElementsByTagName('tr');
        iRowCount           = oRows.length-1;
        for(let j = iRowCount ; j>=1; j--)//LIMPIANDO LA TABLA PARA RECIBIR NUEVOS DATOS
        {
           document.getElementById("TABLEPRODUCTOS").deleteRow(j);
           iRowCount = 0;
        }
    }
    setcontadorefecto(contadorefecto+1);
}
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
useEffect(() => 
{
    SELECT_TODOSLOSPRODUCTOS();
    SELECT_UNIDADOPERATIVA();
    FUNCION_EXTRAER_BODEGAS();
   
}, [contadorefecto])
    return (
        <>
        <div className="row tipoletra">
        
    
    <div className='col-sm-4'> 
                <button type="button" className="btn btn-success btn-sm tipoletra" onClick={ENVIANDO_DATOS}>
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-send-plus" viewBox="0 0 16 16">
                <path d="M15.964.686a.5.5 0 0 0-.65-.65L.767 5.855a.75.75 0 0 0-.124 1.329l4.995 3.178 1.531 2.406a.5.5 0 0 0 .844-.536L6.637 10.07l7.494-7.494-1.895 4.738a.5.5 0 1 0 .928.372l2.8-7Zm-2.54 1.183L5.93 9.363 1.591 6.602l11.833-4.733Z"/>
                <path d="M16 12.5a3.5 3.5 0 1 1-7 0 3.5 3.5 0 0 1 7 0Zm-3.5-2a.5.5 0 0 0-.5.5v1h-1a.5.5 0 0 0 0 1h1v1a.5.5 0 0 0 1 0v-1h1a.5.5 0 0 0 0-1h-1v-1a.5.5 0 0 0-.5-.5Z"/>
                </svg> ENVIAR SOLICITUD PARA ENVIO DE ARTICULOS
                </button>
    </div>
    <div className='col-sm-6'>
                <button className="btn btn-warning tipoletra" onClick={BORRAR_TABLA} title="Actualizar Tabla" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-eraser-fill" viewBox="0 0 16 16">
                <path d="M8.086 2.207a2 2 0 0 1 2.828 0l3.879 3.879a2 2 0 0 1 0 2.828l-5.5 5.5A2 2 0 0 1 7.879 15H5.12a2 2 0 0 1-1.414-.586l-2.5-2.5a2 2 0 0 1 0-2.828l6.879-6.879zm.66 11.34L3.453 8.254 1.914 9.793a1 1 0 0 0 0 1.414l2.5 2.5a1 1 0 0 0 .707.293H7.88a1 1 0 0 0 .707-.293l.16-.16z"/>
                </svg>
                BORRAR PEDIDO ANTERIOR
                </button>
    </div>
    <div className='col-auto'>
    <button type="button" className="btn btn-primary btn-sm tipoletra" onClick={MODALNUEVACOMPRAREGISTRAR}     data-toggle="modal"  data-target   ="#MODALBUSCANDOPRODUCTO">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-search" viewBox="0 0 16 16">
                <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"/>
                </svg> AGREGAR PRODUCTO
                </button>
    </div>
  </div>
  <br></br>
            <div className="container">
            <div className=" ">
<div id="RPRODUCTO" className="row ">
            
            <div id=""  className="col-sm-3 tipoletra colorfondobarra">
            <div className='row tipoletra'>
            <div className="w-100">
                    <div className="form-group">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-exclamation-circle-fill coloricono" viewBox="0 0 16 16">
                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4zm.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2z"/>  
                    </svg>
                    <label for="exampleFormControlSelect1" className='tipoletra2'> SELECCIONAR UNIDAD OPERATIVA   
                    </label>
                    <select className="form-control form-control-sm" id="unidadoperativaid" onClick={FUNCION_EXTRAER_BODEGAS}>
                    {unidadoperativa.map(data_unidadoperativa => 
                    {
                       return (
                        <option value={data_unidadoperativa.id}>{data_unidadoperativa.nombre}</option>
                       );
                    })}   
                    </select>
                    </div>
            </div>
            <div className='w-100'>
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-exclamation-circle-fill coloricono" viewBox="0 0 16 16">
                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4zm.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2z"/>  
                </svg>
                <label for="exampleFormControlInput1" className="form-label tipoletra2">SELECCIONE BODEGA DE ALOJAMIENTO :  </label>
                <select className="form-control form-control-sm tipoletra" id="IDBODEGAALIJAMIENTO" aria-label="Default select example">
                {bodegaunidadOpera.map(data_bodega => 
                   {
                       return (
                        <option value={data_bodega.id} selected>{data_bodega.nombre}</option>
                        
                       );
                   })}
                </select>
            </div>
            <div className="w-100">
                <button type="button" className="btn btn-danger btn-sm form-label tipoletra" onClick={FUNCION_EXTRAER_BODEGAS} title="ACTUALIZAR SERIE" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-clockwise" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                </svg> ACTUALIZAR  BODEGAS
                </button>
            </div>
            <div className='w-100'>
                <div className="form-group">
                    <label for="exampleFormControlTextarea1" className='tipoletra2'>AGREGAR OBSERVACIONES</label>
                    <textarea className="form-control tipoletra" id="observacionpeticion" rows="1"></textarea>
                </div>
            </div>
            <div className='w-100'>
                <div className="col">
                    <label for="exampleFormControlInput1" className="form-label tipoletra2">FECHA CREACIÓN: </label>
                    <input type="text" disabled className="form-control tipoletra" id="IDFECHACRECION"  required   placeholder="FECHA DE CREACION" />
                </div>
            </div>
            <div className='w-100'>
               
            </div>
            <div className='w-100'>
                
            </div>
            </div>
            </div>
            <div id=""  className="col-sm-9 ">
                     
                <table className="table table-bordered  table table-hover tipoletra " id="TABLEPRODUCTOS">
                <thead>
                <tr>
                    <th scope="col">#</th>
                    <th scope="col">PRODUCTO</th>
                    <th scope="col">ID PRESENTACIÓN</th>
                    <th scope="col">PRESENTACIÓN</th>
                    <th scope="col">CANTIDAD</th>
                </tr>
                </thead>
                </table>
            </div>
</div>
</div>
</div>

             {/* :::::::::::::::::::: MODAL COMPRAS :::::::::::::::::::::::::::::::::::::::::::  */}
               
             <div className="modal fade" id="MODALBUSCANDOPRODUCTO" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog">
            <div className="modal-content">
            <div className="container py-4">
                
            

            <div className="card-header">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-pen" viewBox="0 0 16 16">
            <path d="m13.498.795.149-.149a1.207 1.207 0 1 1 1.707 1.708l-.149.148a1.5 1.5 0 0 1-.059 2.059L4.854 14.854a.5.5 0 0 1-.233.131l-4 1a.5.5 0 0 1-.606-.606l1-4a.5.5 0 0 1 .131-.232l9.642-9.642a.5.5 0 0 0-.642.056L6.854 4.854a.5.5 0 1 1-.708-.708L9.44.854A1.5 1.5 0 0 1 11.5.796a1.5 1.5 0 0 1 1.998-.001zm-.644.766a.5.5 0 0 0-.707 0L1.95 11.756l-.764 3.057 3.057-.764L14.44 3.854a.5.5 0 0 0 0-.708l-1.585-1.585z"/>
            </svg> AGREGAR PRODUCTO PARA ENVIO
            </div>

            <form className="field"  onSubmit={AGREGANDO_LISTA_PRODUCTOS}>

            <div className="">

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">SELECCIONE PRODUCTO: </label>
            <div className="custom-control custom-switch">
            <select className="form-control form-control-sm" id="PRODUCTOSELCCION" onClick={FUNCION_PROVEEDORES_SELECCIONADOS}  aria-label="Default select example">
            {todoslosproductos.map(data_productos => {
                return(
                    <option value={data_productos.id}>{data_productos.nombre}</option>
                );
            })}
            </select>
            </div>
            </div>

            
            <div className="form-group row">
                
            <div className="col-sm-8">
            <label for="exampleFormControlInput1" className="form-label"><span className="badge bg-primary">SELECCIONE PRESENTACIÓN : </span> </label>
            <select className="form-select" id="IDPRESENTACION" aria-label="Default select example">
           
           {presentacionSelecionada.map(presentacion_data => {
               return( <option value={presentacion_data.id}>{presentacion_data.descripcionpresentacion}</option>);
           })}
            </select>
            </div>

            <div className="col-sm-2">
            <br/>
            <button type="button" className="btn btn-danger btn-sm form-label" onClick={FUNCION_PROVEEDORES_SELECCIONADOS} title="ACTUALIZAR SERIE" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-clockwise" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                </svg> ACTUALIZAR 
            </button>
            </div>
            
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">CANTIDAD A SOLICITAR : </label>
            <input type="number" min="1" className="form-control" id="NOMBREPRECIOLISTA" step="0.01"  maxLength="500"   placeholder="INGRESE CANTIDAD A PEDIR" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlTextarea1" className="form-label">USUARIO-CREADOR: </label>
            <input type="text" className="form-control" disabled value="11" id="HABILITADO"   placeholder />
            </div>

            <div className="modal-footer">
            <button className="btn btn-primary is-success mt-2" >REGISTRAR</button>
            <button className="btn btn-danger is-success mt-2 close"data-dismiss="modal" aria-label="Close" >CERRAR</button>
            </div>
            </div>
            </form>
            </div>
            </div>
            </div>
            </div>


        </>
    )
}

CrearSolicitud.propTypes = {

}

export default CrearSolicitud

