import React,{useState,useEffect} from 'react'
import PropTypes from 'prop-types'
import Navegacion from '../paginainicial/Navegador';
import PiedePagina from '../paginainicial/Piedepagina';
import './StyloProveedores.css';
import imagen from '../../img/repartidor.png';
import Constantes from '../Constante';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useHistory,
  useParams
} from "react-router-dom";
function DatosIndividualesProveedor(props) {
  const [proveedor, setProveedor] = useState([]);
  const {id} = useParams();
  const history = useHistory();
  const menuinicial = () =>{
    history.push("/registroProveedor")
  }

function Cambiandotexto(ubicacion){
 // console.log("ubicacion "+ubicacion);
  const valor = ubicacion;

  if(valor != undefined && valor == 2){
    var element = document.getElementById("Buenas2");
    element.innerHTML = "De exterior";
  }
  else if(valor != undefined && valor == 1)
  {
    var element = document.getElementById("Buenas2");
    element.innerHTML = "Local";
  
  }
}
  const TrayendoDataProducto = () => {
   // console.log(id);
    fetch(`${Constantes.RUTA_API5}/obtener-proveedorid.php`, {
      method: "POST",
      body: id, 
      })
    .then((res) =>res.json())
    .then((data)=>{
    
      setProveedor(data);
     console.log(data);
    });
  }
useEffect(() => {
  TrayendoDataProducto();
  Cambiandotexto();
}, [])
    return (
        <>
            <Navegacion/>
  <div className="container col-sm-12 fuentetexto">
  <button onClick={menuinicial}type="button" className="btn btn-ligth"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-90deg-left" viewBox="0 0 16 16">
  <path fill-rule="evenodd" d="M1.146 4.854a.5.5 0 0 1 0-.708l4-4a.5.5 0 1 1 .708.708L2.707 4H12.5A2.5 2.5 0 0 1 15 6.5v8a.5.5 0 0 1-1 0v-8A1.5 1.5 0 0 0 12.5 5H2.707l3.147 3.146a.5.5 0 1 1-.708.708l-4-4z"/>
  </svg> Regresar...</button> 
  
  <header className="d-flex align-items-center pb-3 mb-5 border-bottom bajandobotonregresar">
    <a href="/" className="d-flex align-items-center text-dark text-decoration-none">
    <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" className="bi bi-journal-medical" viewBox="0 0 16 16">
    <path fill-rule="evenodd" d="M8 4a.5.5 0 0 1 .5.5v.634l.549-.317a.5.5 0 1 1 .5.866L9 6l.549.317a.5.5 0 1 1-.5.866L8.5 6.866V7.5a.5.5 0 0 1-1 0v-.634l-.549.317a.5.5 0 1 1-.5-.866L7 6l-.549-.317a.5.5 0 0 1 .5-.866l.549.317V4.5A.5.5 0 0 1 8 4zM5 9.5a.5.5 0 0 1 .5-.5h5a.5.5 0 0 1 0 1h-5a.5.5 0 0 1-.5-.5zm0 2a.5.5 0 0 1 .5-.5h5a.5.5 0 0 1 0 1h-5a.5.5 0 0 1-.5-.5z"/>
    <path d="M3 0h10a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H3a2 2 0 0 1-2-2v-1h1v1a1 1 0 0 0 1 1h10a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H3a1 1 0 0 0-1 1v1H1V2a2 2 0 0 1 2-2z"/>
    <path d="M1 5v-.5a.5.5 0 0 1 1 0V5h.5a.5.5 0 0 1 0 1h-2a.5.5 0 0 1 0-1H1zm0 3v-.5a.5.5 0 0 1 1 0V8h.5a.5.5 0 0 1 0 1h-2a.5.5 0 0 1 0-1H1zm0 3v-.5a.5.5 0 0 1 1 0v.5h.5a.5.5 0 0 1 0 1h-2a.5.5 0 0 1 0-1H1z"/>
    </svg> <span className="fs-6"> ID PROVEEDOR: {id}</span>
    </a>
  </header>

           <div id="RPRODUCTO" className="row ">
               <div  className="col-sm-3 colorfondoproveedor">
               <h5 className="text-center texto-titulo margen-input">
                
               </h5> 
               <picture>
                 
               <h5 className="textoencabezeraimagen ">
                 {proveedor.map(framework2 => {
                  return(
                        <td>{framework2.nombre_comercial}</td>
                        );
                  })}
               </h5> 
                
               <img src={imagen} className="card-img-top tamanodeimagen" alt="Imagen de Proveedor" />
              
               </picture>
               </div>
               <div className="col-sm-9">
               <div className="accordion" id="accordionExample">
  <div >
    <div className="card-header" id="headingOne">
      <h2 className="mb-0">
        <button className="btn btn-link btn-block text-left" type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
         INFORMACIÓN DEL PROVEEDOR
        </button>
      </h2>
    </div>

    <div id="collapseOne" className="collapse show" aria-labelledby="headingOne" data-parent="#accordionExample">
      <div className="card-body">
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Nombre Legal:  <b>  {framework2.nombre_legal}</b></td>
                        );
        })}</li>
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Nombre Comercial:  <b>{framework2.nombre_comercial}</b></td>
                        );
        })}</li>
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Sitio Web:  <b>{framework2.web}</b></td>
                        );
        })}</li>
         <li>{proveedor.map(framework3 => {
                  return(
                    <td onClick={Cambiandotexto(framework3.ubicacion)}></td>
                   
                        );
        })}</li>
        <li>
        Ubicación:  <b>
             <td id="Buenas2" ></td>
             </b>
        </li>
        </div>
    </div>
  </div>
  <div >
    <div className="card-header" id="headingTwo">
      <h2 className="mb-0">
        <button className="btn btn-link btn-block text-left collapsed" type="button" data-toggle="collapse" data-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
         CONTACTO
        </button>
      </h2>
    </div>
    <div id="collapseTwo" className="collapse" aria-labelledby="headingTwo" data-parent="#accordionExample">
      <div className="card-body">
      <li>{proveedor.map(framework2 => {
                  return(
                        <td>Nombre Contacto:  <b>{framework2.contacto}</b></td>
                        );
        })}</li>
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Telefono 1:  <b>{framework2.telefono1}</b></td>
                        );
        })}</li>
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Telefono 2:  <b>{framework2.telefono2}</b></td>
                        );
        })}</li>
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Telefono 3:  <b>{framework2.telefono3}</b></td>
                        );
        })}</li>
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Email:  <b>{framework2.email}</b></td>
                        );
        })}</li>
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Dirección:  <b>{framework2.direccion}</b></td>
                        );
        })}</li>
      </div>
    </div>
  </div>
  <div >
    <div className="card-header" id="headingThree">
      <h2 className="mb-0">
        <button className="btn btn-link btn-block text-left collapsed" type="button" data-toggle="collapse" data-target="#collapseThree" aria-expanded="false" aria-controls="collapseThree">
          DOCUMENTOS
        </button>
      </h2>
    </div>
    <div id="collapseThree" className="collapse" aria-labelledby="headingThree" data-parent="#accordionExample">
      <div className="card-body">
      <li>{proveedor.map(framework2 => {
                  return(
                        <td>Documento NRC:  <b>{framework2.nrc}</b></td>
                        );
        })}</li>  
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Documento NIT:  <b>{framework2.nit}</b></td>
                        );
        })}</li>  
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Documento DUI:  <b>{framework2.dui}</b></td>
                        );
        })}</li> 
         <li>{proveedor.map(framework2 => {
                  return(
                        <td>CREDITO FISCAL SERIE:  <b>{framework2.serieccf}</b></td>
                        );
        })}</li>
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>NOTA CREDITO SERIE:  <b>{framework2.seriencr}</b></td>
                        );
        })}</li> 
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>COMPROBANTE RETENCIÓN SERIE:  <b>{framework2.seriecrt}</b></td>
                        );
        })}</li>
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>FACTURA DEL CONSUMIDOR SERIE:  <b>{framework2.seriefct}</b></td>
                        );
        })}</li>
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Documento Legal Extra:  <b>{framework2.otrodocumento}</b></td>
                        );
        })}</li> 
        <li>{proveedor.map(framework2 => {
                  return(
                        <td>Fecha Creación:  <b>{framework2.fh_creacion}</b></td>
                        );
        })}</li> 
      </div>
    </div>
  </div>
</div>
               </div>
           </div>
 </div>
            <PiedePagina/>
        </>
    )
}

DatosIndividualesProveedor.propTypes = {

}

export default DatosIndividualesProveedor

