const CREANDOCOMPRADETALLE = (e) =>
{
    e.preventDefault();
    const objetoenviocompradetalle =
    {
        ecompras                        :   e.target[0].value,
        idproducto                        :   e.target[1].value,
        presentacion_producto_id        :   e.target[2].value,
        tipo_impuesto                   :   e.target[4].value,
        cantidad_pedida                 :   e.target[5].value,
        cantidad_inventario             :   e.target[6].value,
        cantidad_facturada              :   e.target[7].value,
        precio_unitario                 :   e.target[8].value,
        bodega_id                       :   e.target[9].value,
        fh_creacion                     :   e.target[10].value,
        fh_actualizacion                :   e.target[11].value,
    }
        const cargaUtil = JSON.stringify(objetoenviocompradetalle);
      // console.log(cargaUtil);
        FUNCION_REGISTRO_COMPRA_DETALLE(cargaUtil);
};
    /********* FUNCION DE CARGA PARA ENVASES ******** */
    const FUNCION_REGISTRO_COMPRA_DETALLE=async(cargaUtil) => 
    {
        const respuesta = await fetch(`${Constantes.RUTA_API6}/guardar-compra-detalle.php`, 
        {
            method: "POST",
            body: cargaUtil,
        });
        const exitoso = await   respuesta.json();
        if (exitoso)
         {
        // console.log("entre a notificacion");
            toast.success('COMPRA DETALLE  REGISTRADA', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            });
        }
        else
        {
            toast.error('REGISTRO NO REALIZADO ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            });
        }
}
