const BuscarCOMPRADETALLE = (e) =>
{
    let valor           = document.getElementById("BUSCARDETALLEDELACOMPRA").value;
    e.preventDefault();
    var codigo          = e.which || e.keyCode;
    if (codigo == 127){
    }
    if(codigo >= 32 && codigo <= 126)
    {
        const bandera   =   1;  
        //console.log(String.fromCharCode(codigo));
        valor           = valor + String.fromCharCode(codigo);
    }
      document.getElementById("BUSCARDETALLEDELACOMPRA").value = valor
      filtrar(document.getElementById("BUSCARDETALLEDELACOMPRA").value);
}
//:::::::::::::::::::::::::::::::FUNCION FILTRAR::::::::::::::::::::::::::::::::::::::::::::::::::::::
const filtrar = (busqueda) => 
{
    const resultadoBusqueda = tabladetalle.filter((elementos) =>
    {
        if(elementos.nombreproductotabla.toLowerCase().toString().includes(busqueda)||elementos.descripcionpresentacion.toLowerCase().toString().includes(busqueda))
        {
            return elementos;
        }
    });
    setBuscar(resultadoBusqueda);
    setCOMPRASDETALLESID(resultadoBusqueda);
}


