import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types'
import Navegacion from '../paginainicial/Navegador';
import Piedepagina from '../paginainicial/Piedepagina';
import FormularioUnidad from './FormularioUnidad';
import TablaDivisiones from './TablaDivisiones';
import TablaUnidadOperativa from './TablaUnidadOperativa';
import Cajas from './Cajas';
import Impresoras from './Impresoras';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function BaseUnidades(props) {
    const [banderadivision,setDivision]                         =   useState(false);
    const [banderaunidadoperativa,setUnidadOperativa]           =   useState(false);
    const [banderaCajas,setCajas]                               =   useState(false);
    const [banderaImpresora,setImpresora]                       =   useState(false);
    const [contadorefecto,setContadorEfecto]                    =   useState(0);
    //::::::::::::::::::::::::: Funcion habilitar tabla envase ::::::::::::::::::::::::
    const divisiones = e => 
    {
        e.preventDefault();
        setDivision(true);
        if(banderadivision)
        {
            const envaseid                  = document.getElementById("DIVISIONES");
            envaseid.style.display          = "";
            const unidadid                  = document.getElementById("UNIDADOPERATIVA");
            unidadid.style.display          = "none";
            const presentaciondid           = document.getElementById("CAJAS");
            presentaciondid.style.display   = "none";
            const productolineaid           = document.getElementById("IMPRESORAS");
            productolineaid.style.display   = "none";
            setDivision(false);
        }
        setContadorEfecto(contadorefecto +1);
    }
    //::::::::::::::::::::::::: Funcion habilitar tabla unidad ::::::::::::::::::::::::
    const unidadoperativa = e => 
    {
        e.preventDefault();
        setUnidadOperativa(true);
        if(banderaunidadoperativa)
        {
            const envaseid                  = document.getElementById("DIVISIONES");
            envaseid.style.display          = "none";
            const unidadid                  = document.getElementById("UNIDADOPERATIVA");
            unidadid.style.display          = "";
            const presentaciondid           = document.getElementById("CAJAS");
            presentaciondid.style.display   = "none";
            const productolineaid           = document.getElementById("IMPRESORAS");
            productolineaid.style.display   = "none";
            setUnidadOperativa(false);
        }
        setContadorEfecto(contadorefecto +1);
    }
    //::::::::::::::::::::::::: Funcion habilitar tabla PRESENTACION ::::::::::::::::::::::::
    const cajas = e => 
    {
        e.preventDefault();
        setCajas(true)
        if(banderaCajas)
        {
            const envaseid                  = document.getElementById("DIVISIONES");
            envaseid.style.display          = "none";
            const unidadid                  = document.getElementById("UNIDADOPERATIVA");
            unidadid.style.display          = "none";
            const presentaciondid           = document.getElementById("CAJAS");
            presentaciondid.style.display   = "";
            const productolineaid           = document.getElementById("IMPRESORAS");
            productolineaid.style.display   = "none";
            setCajas(false);
        }
        setContadorEfecto(contadorefecto +1);
    }
    //::::::::::::::::::::::::: Funcion habilitar tabla PRODUCTO LINEA ::::::::::::::::::::::::
    const impresoras = e => 
    {
        e.preventDefault();
        setImpresora(true)
        if(banderaImpresora)
        {
            const envaseid                  = document.getElementById("DIVISIONES");
            envaseid.style.display          = "none";
            const unidadid                  = document.getElementById("UNIDADOPERATIVA");
            unidadid.style.display          = "none";
            const presentaciondid           = document.getElementById("CAJAS");
            presentaciondid.style.display   = "none";
            const productolineaid           = document.getElementById("IMPRESORAS");
            productolineaid.style.display   = "";
            setImpresora(false);
        }
        setContadorEfecto(contadorefecto +1);
    }
    useEffect(() => {
        if(contadorefecto == 0)
        {
            const envaseid                  = document.getElementById("DIVISIONES");
            envaseid.style.display          = "";
            const unidadid                  = document.getElementById("UNIDADOPERATIVA");
            unidadid.style.display          = "none";
            const presentaciondid           = document.getElementById("CAJAS");
            presentaciondid.style.display   = "none";
            const productolineaid           = document.getElementById("IMPRESORAS");
            productolineaid.style.display   = "none";
        }
      }, [contadorefecto])
    return (
        <>
         <ToastContainer/>
             <Navegacion/>
             
             <div className="container col-sm-12">
             <ul className="nav justify-content-center texto-titulo  separacionmenusuperior">
                <li className="nav-item">
                    <button className="btn colormenutablas btn-sm espaciomenusuperior " onClick={divisiones} title="TABLA DIVISIONES" ><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-shop" viewBox="0 0 16 16">
  <path d="M2.97 1.35A1 1 0 0 1 3.73 1h8.54a1 1 0 0 1 .76.35l2.609 3.044A1.5 1.5 0 0 1 16 5.37v.255a2.375 2.375 0 0 1-4.25 1.458A2.371 2.371 0 0 1 9.875 8 2.37 2.37 0 0 1 8 7.083 2.37 2.37 0 0 1 6.125 8a2.37 2.37 0 0 1-1.875-.917A2.375 2.375 0 0 1 0 5.625V5.37a1.5 1.5 0 0 1 .361-.976l2.61-3.045zm1.78 4.275a1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0 1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0 1.375 1.375 0 1 0 2.75 0V5.37a.5.5 0 0 0-.12-.325L12.27 2H3.73L1.12 5.045A.5.5 0 0 0 1 5.37v.255a1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0zM1.5 8.5A.5.5 0 0 1 2 9v6h1v-5a1 1 0 0 1 1-1h3a1 1 0 0 1 1 1v5h6V9a.5.5 0 0 1 1 0v6h.5a.5.5 0 0 1 0 1H.5a.5.5 0 0 1 0-1H1V9a.5.5 0 0 1 .5-.5zM4 15h3v-5H4v5zm5-5a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-2a1 1 0 0 1-1-1v-3zm3 0h-2v3h2v-3z"/>
</svg> DIVISIONES</button>
                </li>
              
                <li className="nav-item"> 
                    <button className="btn colormenutablas btn-sm espaciomenusuperior espaciomenusuperior"  onClick={unidadoperativa}  title="TABLA UNIDAD OPERATIVAS"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-shop" viewBox="0 0 16 16">
  <path d="M2.97 1.35A1 1 0 0 1 3.73 1h8.54a1 1 0 0 1 .76.35l2.609 3.044A1.5 1.5 0 0 1 16 5.37v.255a2.375 2.375 0 0 1-4.25 1.458A2.371 2.371 0 0 1 9.875 8 2.37 2.37 0 0 1 8 7.083 2.37 2.37 0 0 1 6.125 8a2.37 2.37 0 0 1-1.875-.917A2.375 2.375 0 0 1 0 5.625V5.37a1.5 1.5 0 0 1 .361-.976l2.61-3.045zm1.78 4.275a1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0 1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0 1.375 1.375 0 1 0 2.75 0V5.37a.5.5 0 0 0-.12-.325L12.27 2H3.73L1.12 5.045A.5.5 0 0 0 1 5.37v.255a1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0zM1.5 8.5A.5.5 0 0 1 2 9v6h1v-5a1 1 0 0 1 1-1h3a1 1 0 0 1 1 1v5h6V9a.5.5 0 0 1 1 0v6h.5a.5.5 0 0 1 0 1H.5a.5.5 0 0 1 0-1H1V9a.5.5 0 0 1 .5-.5zM4 15h3v-5H4v5zm5-5a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-2a1 1 0 0 1-1-1v-3zm3 0h-2v3h2v-3z"/>
</svg> UNIDAD OPERATIVAS</button>
                </li>
                <li className="nav-item">
                    <button className="btn colormenutablas btn-sm espaciomenusuperior espaciomenusuperior"       onClick={cajas} title="TABLA CAJAS"><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-inbox-fill" viewBox="0 0 16 16">
  <path d="M4.98 4a.5.5 0 0 0-.39.188L1.54 8H6a.5.5 0 0 1 .5.5 1.5 1.5 0 1 0 3 0A.5.5 0 0 1 10 8h4.46l-3.05-3.812A.5.5 0 0 0 11.02 4H4.98zm-1.17-.437A1.5 1.5 0 0 1 4.98 3h6.04a1.5 1.5 0 0 1 1.17.563l3.7 4.625a.5.5 0 0 1 .106.374l-.39 3.124A1.5 1.5 0 0 1 14.117 13H1.883a1.5 1.5 0 0 1-1.489-1.314l-.39-3.124a.5.5 0 0 1 .106-.374l3.7-4.625z"/>
</svg> CAJAS</button>
                </li>
                <li className="nav-item">
                    <button  className="btn colormenutablas btn-sm espaciomenusuperior"onClick={impresoras} title="TABLA IMPRESORAS" ><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
  <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
  <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
</svg> IMPRESORAS</button>
                </li>
                <li className="nav-item">
                    <a className="nav-link disabled"title="Otras Opciones">Disabled</a>
                </li>
            </ul>

            <div id="RPRODUCTO" class="row ">
            <div  class="col-sm-2 color-fondoMenu">
                <FormularioUnidad/>
            </div>
            <div id="DIVISIONES"  class="col-sm-10 ">
                <TablaDivisiones/>
            </div>
            <div id="UNIDADOPERATIVA"  class="col-sm-10 ">
                <TablaUnidadOperativa/>
            </div>
            <div id="CAJAS"  class="col-sm-10 ">
                <Cajas/>
            </div>
            <div id="IMPRESORAS"  class="col-sm-10 ">
                <Impresoras/>
            </div>
            </div>
            
            </div>
            <Piedepagina/>
        </>
    )
}

BaseUnidades.propTypes = {

}

export default BaseUnidades

