import logo from './logo.svg';
import './App.css';
import Basepagina from './componentes/paginainicial/Basepagina';
import Login from './componentes/login/Login';
import Myperfil from './componentes/myperfil/Perfil';
import RegistoUsuarios from './componentes/registrarusuarios/Principal';
import RegistroProducto from './componentes/registrandoproducto/Bodyregistrando';
import DatoIndividualProducto from './componentes/registrandoproducto/DatosindividualesProductos';
import RegistroProveedor from './componentes/RegistroProveedores/BodyProveedores';
import DatosIndividualesProveedor from './componentes/RegistroProveedores/DatosIndividualesProveedor';
import NormalizacionDatos from './componentes/NormalizacionDatos/BaseNormalizacion';
import BaseUnidades from './componentes/UnidadesOperativas/BaseUnidades';
import Bodegas from './componentes/bodegas/BodegasBase';
import ProcesosIndividuales from './componentes/ProcesosDelSistema/ProcesosIndividuales';
import BodyProcesos from './componentes/ProcesosDelSistema/BodyProcesos';
import BodyCompras from './componentes/RegistrandoCompras/BodyCompras';
import DatosIndividualesCompras from './componentes/RegistrandoCompras/DatosIndividualesCompras';
import CosteoProductos from './componentes/RegistrandoCompras/CosteoProductos';
import BodyInventarioGeneral from './componentes/InventarioGeneral/BodyInventarioGeneral';
import ProductoIndividualesInformacion from './componentes/InventarioGeneral/ProductoIndividualesInformacion';
import BodegaIndividualinformacion from './componentes/InventarioGeneral/BodegaIndividualinformacion';
import BodyEnvios from './componentes/EnviosdeProducto/BodyEnvios';
import InformacionDeEnvios from './componentes/EnviosdeProducto/InformacionDeEnvios';
import InformacionDeRecepcion from './componentes/EnviosdeProducto/InformacionDeRecepcion';
import AgregarProductoExtra from './componentes/EnviosdeProducto/AgregarProductoExtra';
import SubirCompra from './componentes/RegistrandoCompras/SubirCompra';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
const App = () => {
  return (
    <>
  <Router >
  <Switch>
    <Route exact path="/inicio"                         component={Basepagina}/>
    <Route exact path="/"                               component={Login} />
    <Route exact path="/myperfil"                       component={Myperfil} />
    <Route exact path="/bodegas"                        component={Bodegas}/>
    <Route exact path="/DatoIndividualProducto/:id"     component={DatoIndividualProducto}/>
    <Route exact path="/registrando"                    component={RegistoUsuarios}/>
    <Route exact path="/registrandoproducto"            component={RegistroProducto}/>
    <Route exact path="/registroProveedor"              component={RegistroProveedor}/>
    <Route exact path="/DatoIndividual/:id"             component={DatosIndividualesProveedor}/>
    <Route exact path="/DatosMaestros"                  component={NormalizacionDatos}/>
    <Route exact path="/BaseUnidades"                   component={BaseUnidades}/>
    <Route exact path="/ProcesosSistema"                component={BodyProcesos}/>
    <Route exact path="/ProcesosIndividuales/:id"       component={ProcesosIndividuales}/>
    <Route exact path="/DatosCompras/:id"               component={DatosIndividualesCompras}/>
    <Route exact path="/CosteoProductos/:id"            component={CosteoProductos}/>
    <Route exact path="/Producto_Informacion/:id"       component={ProductoIndividualesInformacion}/>
    <Route exact path="/Productos_Bodega/:id"           component={BodegaIndividualinformacion}/>
    <Route exact path="/BodyCompras"                    component={BodyCompras}/>
    <Route exact path="/Inventario"                     component={BodyInventarioGeneral}/>
    <Route exact path="/Envio_Productos"                component={BodyEnvios}/>
    <Route exact path="/InformacionDeEnvios/:id"        component={InformacionDeEnvios}/>
    <Route exact path="/InformacionDeRecepcion/:id"     component={InformacionDeRecepcion}/>
    <Route exact path="/AgregarProductoExtra/:id"       component={AgregarProductoExtra}/>
    <Route exact path="/SubirCompra"                    component={SubirCompra}/>
  </Switch>
  </Router>
    </>
  );
}

export default App;
