import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types'
import Constantes from '../Constante';
import Paginate from '../Paginacion/Paginate';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './Normalizacion.css'
import Loading from '../Loading';
import LoadingModal from '../LoadingModal';
function TablaUnidad(props) {
    const [unidadmedida, setUnidadMedida]       =   useState([]);
    const [busqueda2, setBuscar2]               =   useState([]);
    const [actualizar2, setActualizar2]         =   useState(0);
    const [tabla2, setTabla2]                   =   useState([]);
    const [lodingg, setLodingg] = useState(false)
    const [lodinggModal, setLodinggModal] = useState(false)
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::://
//                  CODIGO PARA PAGINACION CREACION DE ESTADOS                                  //  

const [paginaActual,setPaginaActual]    =   useState(1);
const [cantidadPagina]                  =   useState(100);

const indexUltimaPagina     =   paginaActual*cantidadPagina;
const indexPrimeraPagina    =   indexUltimaPagina   -   cantidadPagina;
const publicacionActual2     =   unidadmedida.slice(indexPrimeraPagina,indexUltimaPagina);

const paginacion              =   pageNumber => setPaginaActual(pageNumber);
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::://
//::::::::::: ESTE ESTADO SIRVE PARA EL MODAL DE ACTUALIZAR ::::::::::::::::::::::::::::::::::::::::::
    let [stateunidad,setStateUnidad] = useState({
        id: '',
        name: '',
    });
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::://
//                                                                                                  //
//                         FUNCIONES PARA CRUD DE UNIDAD                                            //
//                                                                                                  //
//                                                                                                  //
//::::::::::::::: FUNCION PARA SELECCIONAR LOS DATOS DE LA TABLA ::::::::::::::::::::::::::::::::::::::
    const SELECTUNIDADMEDIDA = () =>
    {
        setLodingg(true);
        fetch(`${Constantes.RUTA_API}/obtenerunidad.php`).then((res) =>
        res.json()).then((data)=>{
            setUnidadMedida(data);
            setTabla2(data);
            //console.log(data);
            setLodingg(false);
        });    
        
    }
//::::::::::::::: FUNCION PARA ACTUALIZAR LOS DATOS DE LA TABLA ::::::::::::::::::::::::::::::::::::
    const funcionactualizarunidad = (e) =>
    {
        e.preventDefault();
        //console.log(e.target[0].value); 
    const objetoenvioactualizarunidad =
    {
        id           :      e.target[0].value,
        unidadmedida :      e.target[1].value,
        habilitar    :      e.target[2].value,
        fh_actualizar:      e.target[3].value
    }
    const cargaUtil = JSON.stringify(objetoenvioactualizarunidad);
        //console.log(objetoenvioactualizarunidad);
        funcionUPDATEunidad(cargaUtil);
    };
    const funcionUPDATEunidad= async(cargaUtil) => 
    {
        setLodinggModal(true);
        const respuesta2 = await fetch(`${Constantes.RUTA_API}/actualizar-unidad.php`, {
        method: "POST",
        body: cargaUtil,
    });
    setLodinggModal(false);
    const exitoso2 = await   respuesta2.json();
        if (exitoso2) {
           // console.log("entre a notificacion");
        toast.warn('UNIDAD DE MEDIDA ACTUALIZADA ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
        setActualizar2(actualizar2+1)
    }else
    {
        toast.error('NO SE  ACTUALIZO ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
    });}
    }  
//:::::::::::::::::::::::::::::::::  FUNCION PARA BUSCADOR :::::::::::::::::::::::::::::::::::::::::
const Buscar2 = (e) =>
    {
        let valor = document.getElementById("Buscar2").value;
        e.preventDefault();
        var codigo = e.which || e.keyCode;
        if (codigo == 127){
           //setProducto(tabla);
        }
        if(codigo >= 32 && codigo <= 126){
            const bandera=1;  
            //console.log(String.fromCharCode(codigo));
            valor = valor + String.fromCharCode(codigo);
          }
          document.getElementById("Buscar2").value = valor
          filtrar(document.getElementById("Buscar2").value);
          
    }
//:::::::::::::::::::::::::::::::  FUNCION FILTRAR ::::::::::::::::::::::::::::::::::::::::::::::::::::::
    const filtrar = (busqueda) => 
    {
        const resultadoBusqueda = tabla2.filter((elementos) =>
        {
            if(elementos.id.toString().includes(busqueda)||elementos.nombre.toLowerCase().toString().includes(busqueda)){
                return elementos;
            }
        });
        setBuscar2(resultadoBusqueda);
        setUnidadMedida(resultadoBusqueda);
        //console.log("Busqueda de resultado :"+busqueda);
    }
//::::::::::::::::::::::::::    PASANDO LOS DATOS AL MODAL ACTUALIZAR   ::::::::::::::::::::::::::::::::::
    const showModalEdit2 = (data) =>
    {
        setStateUnidad( stateunidad                             = Object.assign({},(data)));

             const inputId2                                     = document.getElementById("IDU");
             inputId2.value                                     = stateunidad.id;
             const inputProducto2                               = document.getElementById("UNIDAD");
             inputProducto2.value                               = stateunidad.nombre;
             const fechaactual                                  = new Date();
             const estructuraFecha                              = fechaactual.getFullYear()+"-"+(fechaactual.getMonth()+1)+"-"+fechaactual.getDate()+" "+fechaactual.getHours()+":"+fechaactual.getMinutes()+":"+fechaactual.getSeconds();
            document.getElementById("FECHAACTUALMODALUNIDAD").value   = estructuraFecha;
             //const inputDescripcion         = document.getElementById("DescripcionModal");
             //inputDescripcion.value         = state.descripcion;
    }
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//::::::::::::::::::::::::::    PASANDO LOS DATOS AL MODAL BORRAR   ::::::::::::::::::::::::::::::::::::
    const showModalDELETE2 = (data) =>
    {
        setStateUnidad( stateunidad                             = Object.assign({},(data)));

             const inputId2                                     = document.getElementById("IDUDELETE");
             inputId2.value                                     = stateunidad.id;
             const inputProducto2                               = document.getElementById("UNIDADDELETE");
             inputProducto2.value                               = stateunidad.nombre;
             const fechaactual                                  = new Date();
             const estructuraFecha                              = fechaactual.getFullYear()+"-"+(fechaactual.getMonth()+1)+"-"+fechaactual.getDate()+" "+fechaactual.getHours()+":"+fechaactual.getMinutes()+":"+fechaactual.getSeconds();
            document.getElementById("FECHAACTUALMODALUNIDADDELETE").value   = estructuraFecha;
             //const inputDescripcion         = document.getElementById("DescripcionModal");
             //inputDescripcion.value         = state.descripcion;
    }
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
const getTableColor = criterio => {
   //criterio = criterio.toLowerCase()
   if(criterio == 1)
   {
       return { backgroundColor: '#DAF7A6 ', color: '#2e2c2c' }
   }
   else if(criterio == 0){
       return { backgroundColor: '  #e87272  ', color: '#2e2c2c' }
   }
  }
    useEffect(() => {
        SELECTUNIDADMEDIDA();
    }, [actualizar2])
    return (
        <>
        
        <div className="container col-sm-12">
        <div id="" className="row ">
            <div className="col">
                <button className="btn btn-info" onClick={()=>setActualizar2(actualizar2+1)} title="Actualizar Tabla" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-clockwise" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                </svg>
                </button>
            </div>
            <div className="col">
                <div className="input-group mb-3 mb-6">    
                <input type="text" onKeyPress={Buscar2}  className="form-control tamañodelbuscador" id="Buscar2" aria-describedby="basic-addon2"   placeholder="Digite para buscar" title="Digitar para buscar..." />
                <span className="input-group-text" id="basic-addon2" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-keyboard" viewBox="0 0 16 16">
                <path d="M14 5a1 1 0 0 1 1 1v5a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V6a1 1 0 0 1 1-1h12zM2 4a2 2 0 0 0-2 2v5a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V6a2 2 0 0 0-2-2H2z"/>
                <path d="M13 10.25a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm0-2a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-5 0A.25.25 0 0 1 8.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 8 8.75v-.5zm2 0a.25.25 0 0 1 .25-.25h1.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-1.5a.25.25 0 0 1-.25-.25v-.5zm1 2a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-5-2A.25.25 0 0 1 6.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 6 8.75v-.5zm-2 0A.25.25 0 0 1 4.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 4 8.75v-.5zm-2 0A.25.25 0 0 1 2.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 2 8.75v-.5zm11-2a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-2 0a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-2 0A.25.25 0 0 1 9.25 6h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 9 6.75v-.5zm-2 0A.25.25 0 0 1 7.25 6h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 7 6.75v-.5zm-2 0A.25.25 0 0 1 5.25 6h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 5 6.75v-.5zm-3 0A.25.25 0 0 1 2.25 6h1.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-1.5A.25.25 0 0 1 2 6.75v-.5zm0 4a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm2 0a.25.25 0 0 1 .25-.25h5.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-5.5a.25.25 0 0 1-.25-.25v-.5z"/>
                </svg>
                </span>
                </div>
            </div>
        </div>
        <div id="" className="row ">
    <div className="table-responsive-md scroll">      
            <table className="table table-hover table-bordered tipoletra ">
            <thead>
            <tr>
                <th scope="col">#</th>
                <th scope="col"><span className="badge bg-light text-dark"> 
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-pencil-square" viewBox="0 0 16 16">
                <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                <path fill-rule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                </svg> UNIDAD</span></th>
                <th scope="col">ESTADO</th>
                <th scope="col">FECHA CREACIÓN</th>
                <th scope="col">FECHA ACTUALIZACIÓN</th>
                <th scope="col">CONFIGURACIÓN</th>
                {/*<th scope="col">Eliminar</th>*/}
            </tr>
            </thead>
            <tbody>
            {lodingg && <Loading/> }
            {publicacionActual2.map(framework=>{
          return(
            <tr scope="row">
                <td>{framework.id}</td>
                <td>{framework.nombre}</td>
                <td style={getTableColor(framework.habilitado)}></td>
                <td>{framework.fh_creacion} </td>
                <td>{framework.fh_actualizacion}</td>
                <td><button type="button" onClick={()=>showModalEdit2(framework)} data-toggle="modal" data-target="#MODALUPDATEUNIDAD" className="btn colorboton centrandoicono">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-gear" viewBox="0 0 16 16">
                <path d="M8 4.754a3.246 3.246 0 1 0 0 6.492 3.246 3.246 0 0 0 0-6.492zM5.754 8a2.246 2.246 0 1 1 4.492 0 2.246 2.246 0 0 1-4.492 0z"/>
                <path d="M9.796 1.343c-.527-1.79-3.065-1.79-3.592 0l-.094.319a.873.873 0 0 1-1.255.52l-.292-.16c-1.64-.892-3.433.902-2.54 2.541l.159.292a.873.873 0 0 1-.52 1.255l-.319.094c-1.79.527-1.79 3.065 0 3.592l.319.094a.873.873 0 0 1 .52 1.255l-.16.292c-.892 1.64.901 3.434 2.541 2.54l.292-.159a.873.873 0 0 1 1.255.52l.094.319c.527 1.79 3.065 1.79 3.592 0l.094-.319a.873.873 0 0 1 1.255-.52l.292.16c1.64.893 3.434-.902 2.54-2.541l-.159-.292a.873.873 0 0 1 .52-1.255l.319-.094c1.79-.527 1.79-3.065 0-3.592l-.319-.094a.873.873 0 0 1-.52-1.255l.16-.292c.893-1.64-.902-3.433-2.541-2.54l-.292.159a.873.873 0 0 1-1.255-.52l-.094-.319zm-2.633.283c.246-.835 1.428-.835 1.674 0l.094.319a1.873 1.873 0 0 0 2.693 1.115l.291-.16c.764-.415 1.6.42 1.184 1.185l-.159.292a1.873 1.873 0 0 0 1.116 2.692l.318.094c.835.246.835 1.428 0 1.674l-.319.094a1.873 1.873 0 0 0-1.115 2.693l.16.291c.415.764-.42 1.6-1.185 1.184l-.291-.159a1.873 1.873 0 0 0-2.693 1.116l-.094.318c-.246.835-1.428.835-1.674 0l-.094-.319a1.873 1.873 0 0 0-2.692-1.115l-.292.16c-.764.415-1.6-.42-1.184-1.185l.159-.291A1.873 1.873 0 0 0 1.945 8.93l-.319-.094c-.835-.246-.835-1.428 0-1.674l.319-.094A1.873 1.873 0 0 0 3.06 4.377l-.16-.292c-.415-.764.42-1.6 1.185-1.184l.292.159a1.873 1.873 0 0 0 2.692-1.115l.094-.319z"/>
                </svg> 
                </button>
                </td>
                {/*<td><button type="button" onClick={()=>showModalDELETE2(framework)} data-bs-toggle="modal" data-bs-target="#MODALDELETEUNIDAD" className="btn btn-danger">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16">
                <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
                <path fill-rule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
                </svg> 
                </button>
                </td>*/}
            </tr>
              );
            })}
            </tbody>
            </table>
            </div>  
    </div>
    <div className="ex4">
    <Paginate paginaActual={cantidadPagina} TotalPagina={unidadmedida.length} paginacion={paginacion}/>
    </div>
    </div>

        {/*::::::::::::::::::::::::::::: MODAL PARA ACTUALIZAR :::::::::::::::::::::::::::::::::::: */}
        <div className="modal fade textomodal " id="MODALUPDATEUNIDAD" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog">
            <div className="modal-content">
            <div className="container py-4">

            <div className="card-header">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-pen" viewBox="0 0 16 16">
            <path d="m13.498.795.149-.149a1.207 1.207 0 1 1 1.707 1.708l-.149.148a1.5 1.5 0 0 1-.059 2.059L4.854 14.854a.5.5 0 0 1-.233.131l-4 1a.5.5 0 0 1-.606-.606l1-4a.5.5 0 0 1 .131-.232l9.642-9.642a.5.5 0 0 0-.642.056L6.854 4.854a.5.5 0 1 1-.708-.708L9.44.854A1.5 1.5 0 0 1 11.5.796a1.5 1.5 0 0 1 1.998-.001zm-.644.766a.5.5 0 0 0-.707 0L1.95 11.756l-.764 3.057 3.057-.764L14.44 3.854a.5.5 0 0 0 0-.708l-1.585-1.585z"/>
            </svg> ACTUALIZANDO INFORMACIÓN
            </div>

                <form className="field" onSubmit={funcionactualizarunidad}>
                
                <div className="mb-2">
                <label for="exampleFormControlInput1" className="form-label">ID: </label>
                <input type="text" className="form-control" id="IDU" readOnly  placeholder />
                </div>
                
                <div className="mb-2">
                <label for="exampleFormControlInput1" className="form-label">UNIDAD DE MEDIDA: </label>
                <input type="text" className="form-control" id="UNIDAD" required maxLength="150"  placeholder />
                </div>
                
                <div className="mb-2">
                <label className="texto-titulo ">HABILITAR</label>
                <select className="form-select" aria-label="Default select example">
                <option  value="1">SI</option>
                <option value="0">NO</option>
                </select>
                </div>

                <div className="mb-2">
                <label for="exampleFormControlInput1" className="form-label">FECHA ACTUAL: </label>
                <input type="text" className="form-control" id="FECHAACTUALMODALUNIDAD"  disabled  placeholder />
                </div>
                {lodinggModal && <LoadingModal/> }

                <div className="modal-footer">
                <button className="btn btn-warning is-success mt-2" >ACTUALIZAR</button>
                <button className="btn btn-danger is-success mt-2 close"data-dismiss="modal" aria-label="Close" >CERRAR</button>
                </div>

                </form>
        </div>
    </div>
  </div>
</div>

{/*::::::::::::::::::::::::::::: MODAL PARA ELIMINAR :::::::::::::::::::::::::::::::::::: */}
<div className="modal fade" id="MODALDELETEUNIDAD" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog">
            <div className="modal-content">
            <div className="container py-4">
                <form className="field" >
                
                <div className="mb-2">
                <label for="exampleFormControlInput1" className="form-label">Id: </label>
                <input type="text" className="form-control" id="IDUDELETE" readOnly  placeholder />
                </div>
                
                <div className="mb-2">
                <label for="exampleFormControlInput1" className="form-label">Producto: </label>
                <input type="text" className="form-control" id="UNIDADDELETE"   placeholder />
                </div>
                
                <div className="mb-2">
                <label className="texto-titulo ">HABILITAR</label>
                <select className="form-select" aria-label="Default select example">
                <option  value="1">SI</option>
                <option value="0">NO</option>
                </select>
                </div>

                <div className="mb-2">
                <label for="exampleFormControlInput1" className="form-label">FECHA ACTUAL: </label>
                <input type="text" className="form-control" id="FECHAACTUALMODALUNIDADDELETE"  disabled  placeholder />
                </div>
                <button className="btn btn-danger is-success mt-2" >Guardar</button>
                </form>
        </div>
    </div>
  </div>
</div>
        </>
    )
}

TablaUnidad.propTypes = {

}

export default TablaUnidad

