//::::::::::::::: FUNCION PARA SELECCIONAR LOS DATOS DE LA TABLA ::::::::::::::::::::::::::::::::::://
const SELECTCOMPRA = () =>
{
    setLodingg(true)
    fetch(`${Constantes.RUTA_API6}/obtener-compra.php`).then((res) =>
    res.json()).then((data)=>{
    setDATACOMPRA(data);
    setTablaCompras(data);
    //console.log(data);
    setLodingg(false)
});    
}
//::::::::::::::: FUNCION PARA ACTUALIZAR LOS DATOS DE LA TABLA ::::::::::::::::::::::::::::::::::::
const funcionactualizarcompra = (e) =>
{
    e.preventDefault();
    //console.log(e.target[0].value); 
const objetoenvioactualizarcompra =
{
    id                      :      e.target[0].value,
    proveedor_id            :      e.target[1].value,
    fecha_documento         :      e.target[2].value,
    fecha_vencimiento       :      e.target[3].value,
    tipo_documento          :      e.target[4].value,
    serie_documento         :      e.target[5].value,
    numero_documento        :      e.target[7].value,
    iva                     :      e.target[8].value,
    ivaret                  :      e.target[9].value,
    ivapcp                  :      e.target[10].value,
    unidad_operativa_id     :      e.target[11].value,
    estado                  :      e.target[12].value,
    fh_actualizacion        :      e.target[13].value
}
const cargaUtil = JSON.stringify(objetoenvioactualizarcompra);
    //console.log(cargaUtil);
    funcionUPDATECOMPRA(cargaUtil);
};
const funcionUPDATECOMPRA= async(cargaUtil) => 
{
    setLodinggModal(true);
const respuesta =  await fetch(`${Constantes.RUTA_API6}/actualizar-compra.php`, {
    method: "POST",
    body: cargaUtil,
});
setLodinggModal(false);
//console.log("JSON RESPUESTA : "+respuesta);
const exitoso = await   respuesta.json();
if (exitoso) {
   // console.log("entre a notificacion");
toast.warn('COMPRA  ACTUALIZADA ', {
    position: "top-left",
    autoClose: 2000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
});
setActualizar(actualizar+1)


}else
{
toast.error('NO SE  ACTUALIZO ', {
    position: "top-left",
    autoClose: 2000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
});}
}
//:::::::::::::::::::::::::::::::::  FUNCION PARA BUSCADOR ::::::::::::::::::::::::::::::::::::::::::::::::::::::
const BuscarCOMPRA = (e) =>
{
    let valor           = document.getElementById("BuscarCOMPRASTABLA").value;
    e.preventDefault();
    var codigo          = e.which || e.keyCode;
    if (codigo == 127){
    }
    if(codigo >= 32 && codigo <= 126)
    {
        const bandera   =   1;  
        //console.log(String.fromCharCode(codigo));
        valor           = valor + String.fromCharCode(codigo);
    }
      document.getElementById("BuscarCOMPRASTABLA").value = valor
      filtrar(document.getElementById("BuscarCOMPRASTABLA").value);
}
//:::::::::::::::::::::::::::::::FUNCION FILTRAR::::::::::::::::::::::::::::::::::::::::::::::::::::::
const filtrar = (busqueda) => 
{
    const resultadoBusqueda = tablacompra.filter((elementos) =>
    {
        if(elementos.id.toString().includes(busqueda)||elementos.tipo_documento.toLowerCase().toString().includes(busqueda))
        {
            return elementos;
        }
    });
    setBuscar(resultadoBusqueda);
    setDATACOMPRA(resultadoBusqueda);
}


