let tipodedocumento = "";
const FUNCION_PROVEEDORES_SELECCIONADOS= (cargaUtil) => 
{  
    if(document.getElementById("SERIESTIPODOCUMENTOUDP").value != null) {
        fetch(`${Constantes.RUTA_API5}/obtener-proveedorid.php`, {
            method: "POST",
            body: cargaUtil, 
            })
        .then((res) =>res.json())
        .then((data)=>{
          
            setProveedorID(data);
         tipodedocumento   =   document.getElementById("SERIESTIPODOCUMENTOUDP").value;
        //console.log("TIPO DOCUMENTO SELECCIONADO:"+tipodedocumento);
        switch (tipodedocumento)
        {
            case "CCF":
                var valorserie = "";
                proveedorid.map(datossacados=>{
                    return(
                       // console.log("DATOS SACADOS DE LA PETICION: "+datossacados.serieccf),
                        valorserie = datossacados.serieccf
                    );
                })
                document.getElementById("NUMEROSERIEUDP").value = valorserie;
            break;
            case "CFT":
                var valorserie = "";
                proveedorid.map(datossacados=>{
                    return(
                        valorserie = datossacados.seriefct
                    );
                })
                document.getElementById("NUMEROSERIEUDP").value = valorserie;
            break;
            case "FXC":
                var valorserie = "";
                proveedorid.map(datossacados=>{
                    return(
                        valorserie = datossacados.serieccf
                    );
                })
                document.getElementById("NUMEROSERIEUDP").value = "";
            break;
            case "NCR":
                var valorserie = "";
                proveedorid.map(datossacados=>{
                    return(
                        valorserie = datossacados.seriencr
                    );
                })
                document.getElementById("NUMEROSERIEUDP").value = valorserie;
            break;
            case "CRT":
                var valorserie = "";
                proveedorid.map(datossacados=>{
                    return(
                        valorserie = datossacados.seriecrt
                    );
                })
                document.getElementById("NUMEROSERIEUDP").value = valorserie;
            break;
            default:
                document.getElementById("NUMEROSERIEUDP").value = "SELECCIONE TIPO DOCUMENTO...";
            break;
        }
            
        });
    }
  
}
