//::::::::::::::: FUNCION PARA SELECCIONAR LOS DATOS DE LA TABLA ::::::::::::::::::::::::::::::::::://
const SELECTIMPRESORA = () =>
{
    setLodingg(true)
    fetch(`${Constantes.RUTA_API2}/obtenerimpresora.php`).then((res) =>
    res.json()).then((data)=>{
    setImpresora(data);
    setTablaIMPRESORA(data);
    //console.log(data);
    setLodingg(false)
});    
}
//::::::::::::::: FUNCION PARA ACTUALIZAR LOS DATOS DE LA TABLA ::::::::::::::::::::::::::::::::::::
const funcionactualizarIMPRESORA = (e) =>
{
    e.preventDefault(); 
const objetoenvioactualizarimpresora =
{
    id                              :      e.target[0].value,
    unidad_operativa_id             :      e.target[1].value,
    nombre                          :      e.target[2].value,
    ip                              :      e.target[3].value,
    tipo                            :      e.target[4].value,
    habilitado                      :      e.target[5].value,
    fh_actualizacion                :      e.target[6].value,

}
const cargaUtil = JSON.stringify(objetoenvioactualizarimpresora);
    //console.log(objetoenvioactualizarimpresora);
    funcionUPDATEIMPRESORA(cargaUtil);
};
const funcionUPDATEIMPRESORA= async(cargaUtil) => 
{
    setLodinggModal(true);
    const respuesta =  await fetch(`${Constantes.RUTA_API2}/actualizar-impresora.php`, {
    method: "PUT",
    body: cargaUtil,
});
setLodinggModal(false);
//console.log("JSON RESPUESTA : "+respuesta.json());
        const exitoso = await   respuesta.json();
if (exitoso) 
{
// console.log("entre a notificacion");
toast.warn('IMPRESORA ACTUALIZADA ', {
position: "top-left",
autoClose: 2000,
hideProgressBar: false,
closeOnClick: true,
pauseOnHover: true,
draggable: true,
progress: undefined,
});
setActualizar4(actualizar4+1)
}
else
{
    toast.error('NO SE  ACTUALIZO ', {
    position: "top-left",
    autoClose: 2000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
}); 
}
}     

//:::::::::::::::::::::::::::::::::  FUNCION PARA BUSCADOR ::::::::::::::::::::::::::::::::::::::::::::::::::::::
const Buscar4 = (e) =>
{
    let valor           = document.getElementById("Buscar4").value;
    e.preventDefault();
    var codigo          = e.which || e.keyCode;
    if (codigo == 127){
    }
    if(codigo >= 32 && codigo <= 126)
    {
        const bandera   =   1;  
       // console.log(String.fromCharCode(codigo));
        valor           = valor + String.fromCharCode(codigo);
    }
      document.getElementById("Buscar4").value = valor
      filtrar2(document.getElementById("Buscar4").value);
}
//:::::::::::::::::::::::::::::::FUNCION FILTRAR::::::::::::::::::::::::::::::::::::::::::::::::::::::
const filtrar2 = (busqueda) => 
{
    const resultadoBusqueda = tablaimpresora.filter((elementos) =>
    {
        if(elementos.nombreunidadoperativa.toLowerCase().toString().includes(busqueda)||elementos.nombre.toLowerCase().toString().includes(busqueda)||elementos.unidad_operativa_id.toString().includes(busqueda)||elementos.ip.toLowerCase().toString().includes(busqueda)||elementos.tipo.toLowerCase().toString().includes(busqueda)||elementos.habilitado.toString().includes(busqueda))
        {
            return elementos;
        }
    });
    setBuscar4(resultadoBusqueda);
    setImpresora(resultadoBusqueda);
}
