const [lodinggModal, setLodinggModal] = useState(false)
const CARGANDO_TODO_COMPRASDETALLE = () =>
{
  setLodinggModal(true)
    setLodingg(true)
    fetch(`${Constantes.RUTA_API6}/obtener-compradetalle-id.php`, {
        method: "POST",
        body: id, 
        })
    .then((res) =>res.json())
    .then((data)=>{
        setCOMPRASDETALLESID(data);
        setDATACOMPRADETALLE(data);
        setLodingg(false)
        setLodinggModal(false)
    });
}
//                          CALCULANDO COSTEO INDIVIDUAL               
function COSTEANDO_PRODUCTO(presentacion_compra,id_input,id_producto,id_presentacioninventariopro,precioanterior)
{ 
  const objetoenviandodatos =
  {
      presentacioncompra                  :     presentacion_compra,
      idcompradetalle                     :     id_input,
      idproducto                          :     id_producto,
      idpresentacioninventario            :     id_presentacioninventariopro,
      precio_anterior                     :     precioanterior
  }
  const cargaUtil = JSON.stringify(objetoenviandodatos);
  setLodinggModal(true)
  fetch(`${Constantes.RUTA_API6}/obtener-costeo.php`, {
    method: "POST",
    body:cargaUtil, 
    })
.then((res) =>res.json())
.then((data)=>{
    var element = document.getElementById(id_input);
    element.innerHTML = "$"+data;
    setLodinggModal(false)
});
}
//                          CALCULANDO COSTEO GENERAL               
function COSTEANDO_TODS_LOS_PRODUCTOS()
{ 
  comprasdetalleid.map(data_compradetalle => {
    //console.log(data_compradetalle.idpresentacionproducto);
    //data_compradetalle.idpresentacionproducto,data_compradetalle.id,data_compradetalle.idproducto,data_compradetalle.idpresentacion_inventario,data_compradetalle.PRECIOUNITARIOANTERIOR
  
  const objetoenviandodatos =
  {
      presentacioncompra                  :     data_compradetalle.idpresentacionproducto,
      idcompradetalle                     :     data_compradetalle.id,
      idproducto                          :     data_compradetalle.idproducto,
      idpresentacioninventario            :     data_compradetalle.idpresentacion_inventario,
      precio_anterior                     :     data_compradetalle.PRECIOUNITARIOANTERIOR
  }
  const cargaUtil = JSON.stringify(objetoenviandodatos);
 // console.log(cargaUtil);
  setLodinggModal(true)
  fetch(`${Constantes.RUTA_API6}/obtener-costeo.php`, {
    method: "POST",
    body:cargaUtil, 
    })
.then((res) =>res.json())
.then((data)=>{
    var element = document.getElementById(data_compradetalle.id);
    element.innerHTML = "$"+data;
    setLodinggModal(false)
});
})
}
//                                  GUARDANDO EL COSTO DEL PRODUCTO 
function CREANDO_NUEVO_COSTO(idCOMPRA,id_producto,costo_actual,precio_unitario)
{
  var content = document.getElementById(idCOMPRA);
  //console.log ("Valor del td: "+content.innerHTML);
  var arrayDeCadenas  = content.innerHTML.split("$"); 
  //console.log(arrayDeCadenas[1]);
  const fechaactual           =   new Date();
  const estructuraFecha       =   fechaactual.getFullYear()+"-"+(fechaactual.getMonth()+1)+"-"+fechaactual.getDate();
    
  const objetoenviandodatos =
  {
      idproducto                          :     id_producto,
      costo_calculado                     :     arrayDeCadenas[1],
      costo_actual                        :     costo_actual,
      precio_unitario                     :     precio_unitario,
      fecha_actualizacion                 :     estructuraFecha
  }
  const cargaUtil = JSON.stringify(objetoenviandodatos);
  FUNCION_REGISTRAR_COSTOS(cargaUtil);
}
const FUNCION_REGISTRAR_COSTOS  = async(cargaUtil) => 
{

  setLodinggModal(true);
const respuesta =  await fetch(`${Constantes.RUTA_API6}/actualizar-costos_en_producto.php`, {
    method: "POST",
    body: cargaUtil,
});
setLodinggModal(false);
const exitoso = await   respuesta.json();
if (exitoso) {
   // console.log("entre a notificacion");
toast.warn('COSTO  ACTUALIZADO ', {
    position: "top-left",
    autoClose: 2000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
});
//setActualizar(actualizar+1)
}else
{
toast.error('NO SE  ACTUALIZO ', {
    position: "top-left",
    autoClose: 2000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
});}
}
