import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types';
import Constantes from '../Constante';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function SubirSolicitud(props) {
var iRowCount = 0;
var indice = 0;
let [contador2, SetContador2] = useState(0);
  //                EXTRAYENDO INFORMACIÓN DEL ARCHIO               //
let objeto = {}
function LIMPIAR_TABLA()
{
    if( document.getElementById('TABLEPRODUCTOS22').getElementsByTagName('tr') != null)//VERIFICO SI EXISTE MI TABLA
    {
        indice = 0;
        var oRows           = document.getElementById('TABLEPRODUCTOS22').getElementsByTagName('tr');
        iRowCount           = oRows.length-1;
        for(let j = iRowCount ; j>=1; j--)//LIMPIANDO LA TABLA PARA RECIBIR NUEVOS DATOS
        {
            document.getElementById("TABLEPRODUCTOS22").deleteRow(j);
            iRowCount = 0;
        }
    }
}
function funcioncargando()
{
        LIMPIAR_TABLA()
        let fileInput           = document.getElementById('fileInput');//LEEMOS LOS DATOS DEL INPUT
        let fileDisplayArea     = document.getElementById('fileDisplayArea');//INICIALIZAMOS EL AREA A PINTAR 
        fileInput.addEventListener('change', function(e) 
        {
            let file = fileInput.files[0];
            let textType = /text.*/;
            if (file.type.match(textType))
            {
                let reader = new FileReader();      
                reader.onload = function(e) 
                {
                    objeto = reader.result;
                    //fileDisplayArea.innerText = reader.result;
                    const cargaUtil = JSON.stringify(objeto);
                    var TAB = String.fromCharCode(9);//TAB
                    var NUEVALINEA = String.fromCharCode(10);//NUEVALINEA
                    dividirCadena(objeto,NUEVALINEA,TAB);
                }
                reader.readAsText(file);  
            }
            else
            {
                fileDisplayArea.innerText = "xxxx ARCHIVO NO VALIDO xxxx";
            }
        });
  }
  //:::::::::::::::::::: PROBANDO SEPARADOR ::::::::::::::::::::::::::::::::::::::::::::::::::::::

var datosindividuales     = [];
const dividirCadena = (cadenaADividir,NUEVALINEA,TAB) => 
{

    LIMPIAR_TABLA();
    let filasMatriz           = cadenaADividir.split(NUEVALINEA); 
    let columnasMatriz        = [];//CREANDO UN ARREGLO CON EL NUMERO DE FILAS QUE TIENE LA MATRIZ ARCHIVO
    let presentacionproducto  = [];
    let presentacionindividua = [];
    for (let i    =   3; i < filasMatriz.length; i++) 
    {
        columnasMatriz[i-3] = filasMatriz[i].split(TAB)+",";
        console.log("FilasMatriz[]"+filasMatriz.length);
    }
    for (let j    =   0; j < columnasMatriz.length-1; j++) 
    {
        presentacionproducto[j] = columnasMatriz[j].split(",");
        console.log("columnasMatriz[]"+columnasMatriz.length);
    }
    for(let k= 1; k<presentacionproducto.length-1;k++ )
    {
        if(presentacionproducto[k][0].trim() === "")
        {
            //console.log("vacio");
        }
        else
        {
            if(presentacionproducto[k][3].trim() > 0)
            {
                console.log("presentacionproducto[]"+presentacionproducto.length);
                //console.log("ID_PRESENTACION: "+presentacionproducto[k][0].trim());
                //console.log("CANTIDAD: "+presentacionproducto[k][3].trim());
                datosindividuales[indice] = presentacionproducto[k][3].trim();
                indice++;
                console.log("INDICE: "+indice);
                AGREGANDO_LISTA_PRODUCTOS(presentacionproducto[k][0].trim(),presentacionproducto[k][1].trim(),presentacionproducto[k][2].trim(),presentacionproducto[k][3].trim())
            }
        }
    } 
}
// DECLARACION DE VARIABLES :
let celda;
let fila;
let textoCelda;
let combo;
let combo2;
let selected;
let selected2;
let tblBody = "";
let tabladeproductos;
const AGREGANDO_LISTA_PRODUCTOS = (ID_presentacion,PRODUCTO,PRESENTACION,cantidad) => 
{        
    //  SetArreglo(arreglo+1);//GENEREO UN CONTADOR CADA VEZ QUE EL USUARIO INGRESA
    combo               =   PRODUCTO;//REFERENCIA A SELECT DEL PRODUCTO
    selected            =   combo;// TOMO SU VALOR DE TEXTO
    combo2              =   ID_presentacion;//REFERENCIA A SELECT DE LAS PRESENTACIONES
    selected2           =   combo2;//TOMO SU VALOR DE TEXTO
    tabladeproductos    =   document.getElementById("TABLEPRODUCTOS22");//REFERENCIA A TABLA 
    tblBody             =   document.createElement("tbody");//CREO UN ELEMENTO TBODY PARA LA TABLA
    fila                =   document.createElement("tr");//CREO UN ELEMENTO TR PARA LA TABLA
    //------------ CREACION DE LA PRIMERA FILA LOS DATOS QUE TENDRA         //
   
    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(selected);//AGREGO EL TEXTO DEL PRODUCTO SELECCIONADO
    celda.appendChild(textoCelda);//AGREGO EL TEXTO A LA NUEVA CELDA CREADA
    fila.appendChild(celda);//AGREGO A TR LA NUEVA CELDA CREADA CON SU TEXTO
    
    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(PRESENTACION);//AGREGO A LA CELDA EL TEXTO DE LA PRESENTACION
    celda.appendChild(textoCelda);//AGREGO LA CELDA EL TEXTO CREADO
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA


    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(selected2);//AGREGO A LA CELDA EL TEXTO DE LA PRESENTACION
    celda.appendChild(textoCelda);//AGREGO LA CELDA EL TEXTO CREADO
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA
    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(cantidad);//AGREGO EL TEXTO DE LA CANTIDAD
    celda.appendChild(textoCelda);//AGREGO EL TEXTO A LA NUEVA CELDA CREADA
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA 
    tblBody.appendChild(fila);//AGREGO A TBODY EL TR CON SUS CELDAS
    tabladeproductos.appendChild(tblBody); //AGREGO EL TBODY A LA TABLA
}
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                   EXTRAYENDO BODEGAS DE UNIDAD OPERATIVA 
const[bodegaunidadOpera, setBODEGAUNIDAD_OPERA] = useState([])
const FUNCION_EXTRAER_BODEGAS= () => 
 {  
   let idunidad = document.getElementById("unidadoperativaid2").value;
    // console.log(idunidad);
    fetch(`${Constantes.RUTA_API6}/obtener-bodegaunidadoperativa.php`, {
        method: "POST",
        body: idunidad, 
        })
    .then((res) =>res.json())
    .then((data)=>{
        setBODEGAUNIDAD_OPERA(data);
    });
 }
 //                           EXTRAYENDO INFORMACIÓN DE LAS UNIDADES OPERATIVAS     
const [unidadoperativa, setUnidadOperativa] =   useState([]);
const SELECT_UNIDADOPERATIVA = () =>
{
    fetch(`${Constantes.RUTA_API8}/obtener-unidadoperativa.php`).then((res) =>
    res.json()).then((data)=>{
    setUnidadOperativa(data);
    });    
}
//              OBTENIENDO LOS DATOS DE UNA TABLA 
const[id_envio, SetId_ENVIO] = useState(0)
const ENVIANDO_DATOS2 = () => {
   
    let unidad_operativo    =   document.getElementById("unidadoperativaid2").value;
    let OBSERVACIONES       =   document.getElementById("observacionpeticion2").value;
    let FECHACREACION       =   document.getElementById("fecha_actual").value;
    let BODEGA              =   document.getElementById("IDBODEGAALIJAMIENTO2").value;
    if( document.getElementById('TABLEPRODUCTOS22').getElementsByTagName('tr') != null && document.getElementById('TABLEPRODUCTOS22').getElementsByTagName('tr').length > 1)//VERIFICO SI EXISTE MI TABLA
    {
        var oRows           = document.getElementById('TABLEPRODUCTOS22').getElementsByTagName('tr');
        iRowCount           = oRows.length-1;
    }
    else
    {
        toast.error('NO EXISTE REGISTRO ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
    if( unidad_operativo == "" || FECHACREACION == "" || BODEGA == "" || iRowCount ==0)//VERIFICO SI LOS CAMPOS REQUERIDOS EXISTEN
    {   //NOTIFICO SI NO EXISTEN LOS DATOS :::::::::::::::::::::::::::::::::::::::::::::::::::
        toast.error('¡¡ ERROR !! INGRESAR DATOS NECESARIOS ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
    else 
    {
        const objeto_envios =
        {
            unidad_operativa        :   unidad_operativo,
            observacionesdeenvio    :   OBSERVACIONES,
            fecha_creacion          :   FECHACREACION,
            bodega                  :  BODEGA,
        }
        oRows = document.getElementById('TABLEPRODUCTOS22').getElementsByTagName('tr');
        iRowCount = oRows.length-1;
        const datos_convertidos = JSON.stringify(objeto_envios);
        fetch(`${Constantes.RUTA_API8}/guardar_envio.php`, {
            method: "POST",
            body:datos_convertidos, 
            })
            .then((res) =>res.json())
            .then((data)=>{
            //console.log(data);
            SetId_ENVIO(data); 
        //setLodingg(true);
        for(let i = 1; i<= (iRowCount); i++)
        {
            
            console.log("ID_DE_ENVIO:"+data);
            let t = document.getElementById("TABLEPRODUCTOS22");
            const objeto_envios_detalle =
            {
                envios_id                   :   data,
                presentacion_producto_id    :   t.rows[i].cells[2].innerText,
                cantidad_pedida             :   t.rows[i].cells[3].innerText,
                bodega_id_origen            :   BODEGA,
                fh_creacion                 :   FECHACREACION,
            }
             const pasando_objetoJSON = JSON.stringify(objeto_envios_detalle);
             //console.log("SOY CARGA UTIL"+ cargaUtil2);
            fetch(`${Constantes.RUTA_API8}/guardar_envio_detalle.php`, {
                method: "POST",
                body:pasando_objetoJSON, 
            })
        }
        toast.success('PETICIÓN REGISTRADA CON EXITO ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            });
        });
    } 
}
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
useEffect(() => {
  //SELECT_TODOSLOSPRODUCTOS();
  SELECT_UNIDADOPERATIVA();
  FUNCION_EXTRAER_BODEGAS();
  const fechaactual           =   new Date();
  const estructuraFecha       =   fechaactual.getFullYear()+"-"+(fechaactual.getMonth()+1)+"-"+fechaactual.getDate()+" "+fechaactual.getHours()+":"+fechaactual.getMinutes()+":"+fechaactual.getSeconds();;
  document.getElementById("fecha_actual").value = estructuraFecha;
 
}, [])
    return (
        <>
        
        <div class="row tipoletra">
        
        <div class="col">
                <button type="button" class="btn btn-success btn-sm tipoletra" onClick={ENVIANDO_DATOS2}>
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-send-plus" viewBox="0 0 16 16">
                <path d="M15.964.686a.5.5 0 0 0-.65-.65L.767 5.855a.75.75 0 0 0-.124 1.329l4.995 3.178 1.531 2.406a.5.5 0 0 0 .844-.536L6.637 10.07l7.494-7.494-1.895 4.738a.5.5 0 1 0 .928.372l2.8-7Zm-2.54 1.183L5.93 9.363 1.591 6.602l11.833-4.733Z"/>
                <path d="M16 12.5a3.5 3.5 0 1 1-7 0 3.5 3.5 0 0 1 7 0Zm-3.5-2a.5.5 0 0 0-.5.5v1h-1a.5.5 0 0 0 0 1h1v1a.5.5 0 0 0 1 0v-1h1a.5.5 0 0 0 0-1h-1v-1a.5.5 0 0 0-.5-.5Z"/>
                </svg> ENVIAR SOLICITUD PARA ENVIO DE ARTICULOS
                </button> 
                <br/><br/>
        </div>
       
        <div className='col-auto'>
        <div class="col-md-auto"> 
        <div class="form-group color_info">
        <label for="exampleFormControlTextarea1 " className='tipoletra'>
        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-capslock" viewBox="0 0 16 16">
        <path fill-rule="evenodd" d="M7.27 1.047a1 1 0 0 1 1.46 0l6.345 6.77c.6.638.146 1.683-.73 1.683H11.5v1a1 1 0 0 1-1 1h-5a1 1 0 0 1-1-1v-1H1.654C.78 9.5.326 8.455.924 7.816L7.27 1.047zM14.346 8.5 8 1.731 1.654 8.5H4.5a1 1 0 0 1 1 1v1h5v-1a1 1 0 0 1 1-1h2.846zm-9.846 5a1 1 0 0 1 1-1h5a1 1 0 0 1 1 1v1a1 1 0 0 1-1 1h-5a1 1 0 0 1-1-1v-1zm6 0h-5v1h5v-1z"/>
        </svg> Agregar Archivo:</label>  
        <input onClick={funcioncargando} type="file"  className="btn btn-sm tipoletra" id="fileInput"/>
        </div>
        </div>
        </div>
       
        <div className='col-2'> 
        
        </div>
        </div>
        
<div id="RPRODUCTO" class="row ">
            <div id=""  class="col-sm-3 tipoletra colorfondobarra">
            <div className='row tipoletra'>
            <div class="w-100">
                    <div class="form-group">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-exclamation-circle-fill coloricono" viewBox="0 0 16 16">
                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4zm.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2z"/>  
                    </svg>
                    <label for="exampleFormControlSelect1" className='tipoletra2'> SELECCIONAR UNIDAD OPERATIVA   
                    </label>
                    <select class="form-control form-control-sm" id="unidadoperativaid2" onClick={FUNCION_EXTRAER_BODEGAS}>
                    {unidadoperativa.map(data_unidadoperativa => 
                    {
                       return (
                        <option value={data_unidadoperativa.id}>{data_unidadoperativa.nombre}</option>
                       );
                    })}   
                    </select>
                    </div>
            </div>
            <div className='w-100'>
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-exclamation-circle-fill coloricono" viewBox="0 0 16 16">
                <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4zm.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2z"/>  
                </svg>
                <label for="exampleFormControlInput1" class="form-label tipoletra2">SELECCIONE BODEGA DE ALOJAMIENTO :  </label>
                <select className="form-control form-control-sm tipoletra" id="IDBODEGAALIJAMIENTO2" aria-label="Default select example">
                {bodegaunidadOpera.map(data_bodega => 
                   {
                       return (
                        <option value={data_bodega.id} selected>{data_bodega.nombre}</option>
                        
                       );
                   })}
                </select>
            </div>
            <div class="w-100">
                <button type="button" className="btn btn-danger btn-sm form-label tipoletra" onClick={FUNCION_EXTRAER_BODEGAS} title="ACTUALIZAR SERIE" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-clockwise" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                </svg> ACTUALIZAR  BODEGAS
                </button>
            </div>
            <div className='w-100'>
                <div class="form-group">
                    <label for="exampleFormControlTextarea1" className='tipoletra2'>AGREGAR OBSERVACIONES</label>
                    <textarea class="form-control tipoletra" id="observacionpeticion2" rows="1"></textarea>
                </div>
            </div>
            <div className='w-100'>
                <div class="col">
                    <label for="exampleFormControlInput1" class="form-label tipoletra2">FECHA ACTUAL: </label>
                    <input type="text" disabled class="form-control tipoletra" id="fecha_actual"  required   placeholder="FECHA DE CREACION" />
                </div>
            </div>
            <div className='w-100'>
               
            </div>
            <div className='w-100'>
                
            </div>
            </div>
            </div>
    <div id=""  class="col-sm-9 ">
          <div  class="col-sm-12 archivo_novalido">
     <pre id="fileDisplayArea"></pre> 
            </div>
            <div className='scrooll-tabla'>
            <table class="table table-bordered  table table-hover tipoletra  " id="TABLEPRODUCTOS22">
                <thead>
                <tr>
                    <th scope="col">PRODUCTO</th>
                    <th scope="col">PRESENTACION</th>
                    <th scope="col">CODIGO PRESENTACION</th>
                    <th scope="col">CANTIDAD A SOLICITAR</th>
                </tr>
                </thead>
                </table>
            </div>
            
    </div>
</div>      
        </>
    )
}

SubirSolicitud.propTypes = {

}

export default SubirSolicitud

