import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types';
import Constantes from '../Constante';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useHistory
  } from "react-router-dom";
function AgregarProducto(props) {
    //::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                   EXTRAYENDO BODEGAS DE UNIDAD OPERATIVA 
const[bodegaunidadOpera, setBODEGAUNIDAD_OPERA] = useState([])
const FUNCION_EXTRAER_BODEGAS= () => 
 {  
   let idunidad = document.getElementById("unidadoperativaid2").value;
    // console.log(idunidad);
    fetch(`${Constantes.RUTA_API6}/obtener-bodegaunidadoperativa.php`, {
        method: "POST",
        body: idunidad, 
        })
    .then((res) =>res.json())
    .then((data)=>{
        setBODEGAUNIDAD_OPERA(data);
    });
 }
 //                           EXTRAYENDO INFORMACIÓN DE LAS UNIDADES OPERATIVAS     
const [unidadoperativa, setUnidadOperativa] =   useState([]);
const SELECT_UNIDADOPERATIVA = () =>
{
    fetch(`${Constantes.RUTA_API8}/obtener-unidadoperativa.php`).then((res) =>
    res.json()).then((data)=>{
    setUnidadOperativa(data);
    });    
}
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
const[peticiones, setEnvioPeticion] = useState([])
const BUSCANDO_UNIDAD = (e) =>
{
    e.preventDefault();
    const objetoenvioproveedor =
    {
        unidad_operativa                    :   e.target[0].value,
        fecha_debusqueda                    :   e.target[1].value,
    }
        const cargaUtil = JSON.stringify(objetoenvioproveedor);
        //console.log(cargaUtil);
        fetch(`${Constantes.RUTA_API8}/obtener_solicitudes_no_recibidas.php`, {
            method: "POST",
            body: cargaUtil, 
            })
        .then((res) =>res.json())
        .then((data)=>{
        //    console.log(data)
            if(data == "")
            {
                const unidadid                  = document.getElementById("identificador");
                unidadid.style.display          = "";
            }
            else{
                const unidadid                  = document.getElementById("identificador");
                unidadid.style.display          = "none";
            }
            setEnvioPeticion(data);
        });
}
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
    useEffect(() => {
        //SELECT_TODOSLOSPRODUCTOS();
        SELECT_UNIDADOPERATIVA();
        FUNCION_EXTRAER_BODEGAS();
        const fechaactual           =   new Date();
        const estructuraFecha       =   fechaactual.getFullYear()+"-"+(fechaactual.getMonth()+1)+"-"+fechaactual.getDate()+" "+fechaactual.getHours()+":"+fechaactual.getMinutes()+":"+fechaactual.getSeconds();;
        document.getElementById("fecha_actual").value = estructuraFecha;
       
      }, [])
    return (
        <>
            <div id="RPRODUCTO" className="row ">
            <div id=""  className="col-sm-3 tipoletra colorfondobarra">
            <form onSubmit={BUSCANDO_UNIDAD}>
            <div className='row tipoletra'>
            <div className="w-100">
                    <div className="form-group">
                    <label for="exampleFormControlSelect1" className='tipoletra2'> SELECCIONAR UNIDAD OPERATIVA   
                    </label>
                    <select className="form-control form-control-sm" id="buscarunidad" onClick={FUNCION_EXTRAER_BODEGAS}>
                    {unidadoperativa.map(data_unidadoperativa => 
                    {
                       return (
                        <option value={data_unidadoperativa.id}>{data_unidadoperativa.nombre}</option>
                       );
                    })}   
                    </select>
                    </div>
            </div>
            <div className='w-100'>
                <div className="col">
                    <label for="exampleFormControlInput1" className="form-label tipoletra2">FECHA ACTUAL: </label>
                    <input type="date"  className="form-control tipoletra" id="fechabuscar"  required   placeholder="FECHA DE CREACION" />
                </div>
            </div>
            <div className='w-100'>
                <div className="col">
                    <label for="exampleFormControlInput1" className="form-label tipoletra2">IDENTIFICADOR DE PETICIÓN: </label>
                    <input type="text"  className="form-control tipoletra" id="fechabuscar"  required   placeholder="INGRESE IDENTIFICADOR DE PETICIÓN" />
                </div>
            </div>
            <div className='w-100'>
                <br/>
                <button type="submit" className="btn btn-success btn-sm tipoletra" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-search" viewBox="0 0 16 16">
                <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"/>
                </svg> BUSCAR SOLICITUD
                </button> 
            </div>
            <div className='w-100'>
                
            </div>
            </div>
            </form>
            
            </div>
    <div id=""  className="col-sm-9 ">
          <div  className="col-sm-12 archivo_novalido">
     <pre id="fileDisplayArea"></pre> 
            </div>
            <div className='scrooll-tabla'>
            <table className="table table-bordered  table table-hover tipoletra  " id="TABLEPRODUCTOS22">
                <thead>
                <tr>
                    <th scope="col">UNIDAD OPERATIVA</th>
                    <th scope="col">FECHA DE PEDIDO</th>
                    <th scope="col">ESTADO</th>
                    <th scope="col">VER SOLICITUD</th>
                </tr>
                </thead>
                <tbody>
                    <label for="exampleFormControlInput1" id='identificador' className="form-label"><span className='badge bg-warning text-dark'>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-cone-striped" viewBox="0 0 16 16">
                    <path d="m9.97 4.88.953 3.811C10.159 8.878 9.14 9 8 9c-1.14 0-2.158-.122-2.923-.309L6.03 4.88C6.635 4.957 7.3 5 8 5s1.365-.043 1.97-.12zm-.245-.978L8.97.88C8.718-.13 7.282-.13 7.03.88L6.275 3.9C6.8 3.965 7.382 4 8 4c.618 0 1.2-.036 1.725-.098zm4.396 8.613a.5.5 0 0 1 .037.96l-6 2a.5.5 0 0 1-.316 0l-6-2a.5.5 0 0 1 .037-.96l2.391-.598.565-2.257c.862.212 1.964.339 3.165.339s2.303-.127 3.165-.339l.565 2.257 2.391.598z"/>
                    </svg> NO SE ENCONTRARON SOLICITUDES CON ESTA INFORMACIÓN <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-cone-striped" viewBox="0 0 16 16">
                    <path d="m9.97 4.88.953 3.811C10.159 8.878 9.14 9 8 9c-1.14 0-2.158-.122-2.923-.309L6.03 4.88C6.635 4.957 7.3 5 8 5s1.365-.043 1.97-.12zm-.245-.978L8.97.88C8.718-.13 7.282-.13 7.03.88L6.275 3.9C6.8 3.965 7.382 4 8 4c.618 0 1.2-.036 1.725-.098zm4.396 8.613a.5.5 0 0 1 .037.96l-6 2a.5.5 0 0 1-.316 0l-6-2a.5.5 0 0 1 .037-.96l2.391-.598.565-2.257c.862.212 1.964.339 3.165.339s2.303-.127 3.165-.339l.565 2.257 2.391.598z"/>
                    </svg></span> </label>
                    {peticiones.map(data_peticiones => {
                        return(
                            <tr>
                                <td>{data_peticiones.nombreunidad}</td>
                                <td>{data_peticiones.fh_recepcion_pedido}</td>
                                <td>PENDIENTE</td>
                                <td><Link to={`/AgregarProductoExtra/${data_peticiones.id}`}  className="btn colorbotonesver centrandoicono " >
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-send-plus" viewBox="0 0 16 16">
                        <path d="M15.964.686a.5.5 0 0 0-.65-.65L.767 5.855a.75.75 0 0 0-.124 1.329l4.995 3.178 1.531 2.406a.5.5 0 0 0 .844-.536L6.637 10.07l7.494-7.494-1.895 4.738a.5.5 0 1 0 .928.372l2.8-7Zm-2.54 1.183L5.93 9.363 1.591 6.602l11.833-4.733Z"/>
                        <path d="M16 12.5a3.5 3.5 0 1 1-7 0 3.5 3.5 0 0 1 7 0Zm-3.5-2a.5.5 0 0 0-.5.5v1h-1a.5.5 0 0 0 0 1h1v1a.5.5 0 0 0 1 0v-1h1a.5.5 0 0 0 0-1h-1v-1a.5.5 0 0 0-.5-.5Z"/>
                        </svg>
                        </Link></td>
                            </tr>
                        );
                    })}
                </tbody>
                </table>
            </div>
            
    </div>
</div>

        </>
    )
}

AgregarProducto.propTypes = {

}

export default AgregarProducto

