//:::::::::::::::::::::::::: SUBIENDO INFORMACION DESDE UN TXT :::::::::::::::::::::::::::::::::::::::::::::
let objeto = {};
var iRowCount = 0;
var indice = 0;
function LIMPIAR_TABLA()
{
    if( document.getElementById('TABLEPRODUCTOS23').getElementsByTagName('tr') != null)//VERIFICO SI EXISTE MI TABLA
    {
        indice = 0;
        var oRows           = document.getElementById('TABLEPRODUCTOS23').getElementsByTagName('tr');
        iRowCount           = oRows.length-1;
        for(let j = iRowCount ; j>=1; j--)//LIMPIANDO LA TABLA PARA RECIBIR NUEVOS DATOS
        {
            document.getElementById("TABLEPRODUCTOS23").deleteRow(j);
            iRowCount = 0;
        }
    }
}
function funcioncargando()
{
        LIMPIAR_TABLA();
        let fileInput           = document.getElementById('fileInput');//LEEMOS LOS DATOS DEL INPUT
        let fileDisplayArea     = document.getElementById('fileDisplayArea2');//INICIALIZAMOS EL AREA A PINTAR 
        fileInput.addEventListener('change', function(e) 
        {
            let file = fileInput.files[0];
            let textType = /text.*/;
            if (file.type.match(textType))
            {
                let reader = new FileReader();      
                reader.onload = function(e) 
                {
                    objeto = reader.result;
                    //fileDisplayArea.innerText = reader.result;
                    const cargaUtil = JSON.stringify(objeto);
                    var TAB = String.fromCharCode(9);//TAB
                    var NUEVALINEA = String.fromCharCode(10);//NUEVALINEA
                    dividirCadena(objeto,NUEVALINEA,TAB);
                }
                reader.readAsText(file);  
            }
            else
            {
                fileDisplayArea.innerText = "xxxx ARCHIVO NO VALIDO xxxx";
            }
        });
  }
  var datosindividuales     = [];
const dividirCadena = (cadenaADividir,NUEVALINEA,TAB) => 
{

    LIMPIAR_TABLA();
    let filasMatriz           = cadenaADividir.split(NUEVALINEA); 
    let columnasMatriz        = [];//CREANDO UN ARREGLO CON EL NUMERO DE FILAS QUE TIENE LA MATRIZ ARCHIVO
    let presentacionproducto  = [];
    let presentacionindividua = [];
    for (let i    =   3; i < filasMatriz.length; i++) 
    {
        columnasMatriz[i-3] = filasMatriz[i].split(TAB)+",";
        //console.log("BUENAS"+filasMatriz[i]);
        //console.log("FilasMatriz[]"+filasMatriz.length);
    }
    for (let j    =   0; j < columnasMatriz.length-1; j++) 
    {
        presentacionproducto[j] = columnasMatriz[j].split(",");
        //console.log("BUENAS"+columnasMatriz[j]);
        //console.log("columnasMatriz[]"+columnasMatriz.length);
    }
    for(let k= 1; k<presentacionproducto.length;k++ )
    {
        if(presentacionproducto[k][0].trim() === "")
        {
            //console.log("vacio");
        }
        else
        {
            if(presentacionproducto[k][3].trim() > 0 && presentacionproducto[k][4].trim() > 0 && presentacionproducto[k][5].trim() > 0 && presentacionproducto[k][6].trim() >0)
            {
                if(presentacionproducto[k][7].trim().toUpperCase() == "GR" || presentacionproducto[k][7].trim().toUpperCase() == "EX" || presentacionproducto[k][7].trim().toUpperCase() == "NS" || presentacionproducto[k][7].trim().toUpperCase() == "XC")
                {
                    //console.log("PRESENTACION: "+presentacionproducto[k][0].trim());
                    //console.log("PRODUCTO: "+presentacionproducto[k][1].trim());
                    //console.log("ID_PRESENTACION: "+presentacionproducto[k][2].trim());
                    //console.log("CANTIDAD SOLICITANTE: "+presentacionproducto[k][3].trim());
                    //console.log("CANTIDAD RECIBIDA: "+presentacionproducto[k][4].trim());
                    //console.log("CANTIDAD FACTURADA: "+presentacionproducto[k][5].trim());
                    //console.log("PRECIO UNITARIO: "+presentacionproducto[k][6].trim());
                    //console.log("TIPO IMPUESTO: "+presentacionproducto[k][7].trim());
                    AGREGANDO_LISTA_PRODUCTOS(presentacionproducto[k][0].trim(),presentacionproducto[k][1].trim(),presentacionproducto[k][2].trim(),presentacionproducto[k][3].trim(),presentacionproducto[k][4].trim(),presentacionproducto[k][5].trim(),presentacionproducto[k][6].trim(),presentacionproducto[k][7].trim().toUpperCase());
                }
            }
        }
    } 
}
// DECLARACION DE VARIABLES :
let celda;
let fila;
let textoCelda;
let combo;
let combo2;
let selected;
let selected2;
let tblBody = "";
let tabladeproductos;
const AGREGANDO_LISTA_PRODUCTOS = (ID_presentacion,PRODUCTO,PRESENTACION,cantidadsolicitada,cantidadrecibida,cantidadfacturada,preciounitario,tipoimpuesto) => 
{        
    //  SetArreglo(arreglo+1);//GENEREO UN CONTADOR CADA VEZ QUE EL USUARIO INGRESA
    combo               =   PRODUCTO;//REFERENCIA A SELECT DEL PRODUCTO
    selected            =   combo;// TOMO SU VALOR DE TEXTO
    combo2              =   ID_presentacion;//REFERENCIA A SELECT DE LAS PRESENTACIONES
    selected2           =   combo2;//TOMO SU VALOR DE TEXTO
    tabladeproductos    =   document.getElementById("TABLEPRODUCTOS23");//REFERENCIA A TABLA 
    tblBody             =   document.createElement("tbody");//CREO UN ELEMENTO TBODY PARA LA TABLA
    fila                =   document.createElement("tr");//CREO UN ELEMENTO TR PARA LA TABLA
    //------------ CREACION DE LA PRIMERA FILA LOS DATOS QUE TENDRA         //
   
    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(ID_presentacion);//AGREGO EL TEXTO DEL PRODUCTO SELECCIONADO
    celda.appendChild(textoCelda);//AGREGO EL TEXTO A LA NUEVA CELDA CREADA
    fila.appendChild(celda);//AGREGO A TR LA NUEVA CELDA CREADA CON SU TEXTO
    
    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(PRODUCTO);//AGREGO A LA CELDA EL TEXTO DE LA PRESENTACION
    celda.appendChild(textoCelda);//AGREGO LA CELDA EL TEXTO CREADO
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA


    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(PRESENTACION);//AGREGO A LA CELDA EL TEXTO DE LA PRESENTACION
    celda.appendChild(textoCelda);//AGREGO LA CELDA EL TEXTO CREADO
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA


    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(cantidadsolicitada);//AGREGO EL TEXTO DE LA CANTIDAD
    celda.appendChild(textoCelda);//AGREGO EL TEXTO A LA NUEVA CELDA CREADA
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA 


    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(cantidadrecibida);//AGREGO EL TEXTO DE LA CANTIDAD
    celda.appendChild(textoCelda);//AGREGO EL TEXTO A LA NUEVA CELDA CREADA
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA 


    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(cantidadfacturada);//AGREGO EL TEXTO DE LA CANTIDAD
    celda.appendChild(textoCelda);//AGREGO EL TEXTO A LA NUEVA CELDA CREADA
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA 


    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(preciounitario);//AGREGO EL TEXTO DE LA CANTIDAD
    celda.appendChild(textoCelda);//AGREGO EL TEXTO A LA NUEVA CELDA CREADA
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA 


    celda               = document.createElement("td");//CREO UN ELEMENTO TD DENTRO DE LA TABLA
    textoCelda          = document.createTextNode(tipoimpuesto);//AGREGO EL TEXTO DE LA CANTIDAD
    celda.appendChild(textoCelda);//AGREGO EL TEXTO A LA NUEVA CELDA CREADA
    fila.appendChild(celda);//AGREGO A TR UNA NUEVA CELDA 


    tblBody.appendChild(fila);//AGREGO A TBODY EL TR CON SUS CELDAS
    tabladeproductos.appendChild(tblBody); //AGREGO EL TBODY A LA TABLA
}
