const[id_envio, SetId_ENVIO] = useState(0)
    let selecciondeProveedores     ;
    let fechaContable              ;
    let fechaDocumento             ;
    let fechaVencimiento           ;
    let numeroDocumento            ;
    let tipo_documento             ;
    let numeroSerie                ;
    let seleciondeUnidadoperati    ;
    let selecciondeBodegas         ;
    let valorIva                   ;
    let valorRetenido              ;
    let valorPercibido             ;
    let fechaactual                ;
const ENVIANDO_DATOS_COMPRA = () => {
   
    selecciondeProveedores      =   document.getElementById("proveedorselec").value;
    fechaContable               =   document.getElementById("FH_COMPRASUBIR").value;
    fechaDocumento              =   document.getElementById("FH_DOCUMENTO").value;
    fechaVencimiento            =   document.getElementById("FH_VENCIMIENTO").value;
    tipo_documento              =   document.getElementById("SERIESTIPODOCUMENTO").value;
    numeroSerie                 =   document.getElementById("NUMERO_SERIE").value;
    seleciondeUnidadoperati     =   document.getElementById("SELECCION_UNIDAD").value;
    selecciondeBodegas          =   document.getElementById("BODEGAALJOA").value;
    valorIva                    =   document.getElementById("VALOR_IVA").value;
    valorRetenido               =   document.getElementById("VALOR_RETENIDO").value;
    valorPercibido              =   document.getElementById("VALOR_PERCIBIDO").value;
    fechaactual                 =   document.getElementById("FH_ACTUAL").value;
    numeroDocumento             =   document.getElementById("NUMERO_DOCUMENTO").value;
   // console.log(document.getElementById('TABLEPRODUCTOS23').getElementsByTagName('tr'));
    if( document.getElementById('TABLEPRODUCTOS23').getElementsByTagName('tr') != null && document.getElementById('TABLEPRODUCTOS23').getElementsByTagName('tr').length > 1 )//VERIFICO SI EXISTE MI TABLA
    {
        var oRows           = document.getElementById('TABLEPRODUCTOS23').getElementsByTagName('tr');
        iRowCount           = oRows.length-1;
        if(selecciondeProveedores == "" || fechaContable == "" || fechaDocumento == "" || fechaVencimiento == "" || tipo_documento == "" || numeroSerie == "" || seleciondeUnidadoperati == "" || selecciondeBodegas == "" ||  valorIva <0 || valorRetenido <0 || valorPercibido <0)
        {
            toast.error('FALTAN DATOS QUE COMPLETAR PARA EL REGISTRO ', {
                position: "top-left",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
        else
        {
            //::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
            //:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::://
            //                                                                          //
            //              FUNCION PARA REGISTRAR DATOS DENTRO DE                      //
            //                    SQL SERVER MEDIANTE MODALES                           //
            //                                                                          //
            //:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::: ::::::::::::://
            //::::::::::::::::::: REGISTRANDO PRODUCTOGENERAL ::::::::::::::::::::::::::::
                const objetoenviocompra =
                {
                    proveedor_id                        :   selecciondeProveedores,
                    fecha_contable                      :   fechaContable,
                    fecha_documento                     :   fechaDocumento,
                    fecha_vencimiento                   :   fechaVencimiento,
                    tipo_documento                      :   tipo_documento,
                    serie_documento                     :   numeroSerie,
                    numero_documento                    :   numeroDocumento,
                    iva                                 :   valorIva,
                    ivaret                              :   valorRetenido,
                    ivapcp                              :   valorPercibido,
                    unidad_operativa_id                 :   seleciondeUnidadoperati,
                    estado                              :   1,
                    fh_creacion                         :   fechaactual,
                    fh_actualizacion                    :   fechaactual,
                }
                const cargaUtil = JSON.stringify(objetoenviocompra);
                fetch(`${Constantes.RUTA_API6}/guardar-compra-desdesubir.php`, {
                    method  :   "POST",
                    body    :   cargaUtil, 
                })
                    .then((res) =>res.json())
                    .then((data)=>{
                   // console.log("buenas man: "+data);
                    SetId_ENVIO(data); 
                    for(let i = 1; i<= (iRowCount); i++)
                    {
                        //console.log("ID_DE_ENVIO:"+data);
                        let t = document.getElementById("TABLEPRODUCTOS23");
                    const objeto_envios_detalle =
                    {
                        /*
                        console.log("PRESENTACION: "+presentacionproducto[k][0].trim());
                        console.log("PRODUCTO: "+presentacionproducto[k][1].trim());
                        console.log("ID_PRESENTACION: "+presentacionproducto[k][2].trim());
                        console.log("CANTIDAD SOLICITANTE: "+presentacionproducto[k][3].trim());
                        console.log("CANTIDAD RECIBIDA: "+presentacionproducto[k][4].trim());
                        console.log("CANTIDAD FACTURADA: "+presentacionproducto[k][5].trim());
                        console.log("PRECIO UNITARIO: "+presentacionproducto[k][6].trim());
                        console.log("TIPO IMPUESTO: "+presentacionproducto[k][7].trim());
                         */
                        ecompras                        :   data,
                        presentacion_producto_id        :   t.rows[i].cells[0].innerText,
                        tipo_impuesto                   :   t.rows[i].cells[7].innerText,
                        cantidad_pedida                 :   t.rows[i].cells[3].innerText,
                        cantidad_inventario             :   t.rows[i].cells[4].innerText,
                        cantidad_facturada              :   t.rows[i].cells[5].innerText,
                        precio_unitario                 :   t.rows[i].cells[6].innerText,
                        bodega_id                       :   selecciondeBodegas,
                        fh_creacion                     :   fechaactual,
                        fh_actualizacion                :   fechaactual,
                    }
                    const pasando_objetoJSON = JSON.stringify(objeto_envios_detalle);
                    //console.log("SOY CARGA UTIL"+ pasando_objetoJSON);
                    fetch(`${Constantes.RUTA_API6}/guardar-compra-detallesubir.php`, {
                        method: "POST",
                        body:pasando_objetoJSON, 
                    })
                    }
                })
                toast.success('COMPRA REGISTRADA CON EXITO ', {
                    position: "top-left",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
        }
    }
    else
    {
        toast.error('NO EXISTE PRODUCTOS EN LA COMPRA ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
}
