//:::::::::::::::::::::::::::::::::  FUNCION PARA VALIDAR ::::::::::::::::::::::::::::::::::::::::::::::::::::::
const validarinputnumeropro1 = (e) =>
{
    let valor           = document.getElementById("CANTIDADCOMBODETALLE").value;
    e.preventDefault();
    var codigo          = e.which || e.keyCode;
   
    if(codigo >= 48 && codigo <= 57 )
    {
        
        //console.log(String.fromCharCode(codigo));
        valor           = valor + String.fromCharCode(codigo);
    }
    
      document.getElementById("CANTIDADCOMBODETALLE").value = valor
}
