const getcombo = criterio => {
    criterio = criterio.toLowerCase()
    if(criterio == 35)
    {
        return { backgroundColor: '#42a952', color: '#f0f7f1' }
    }
    else{
        return { backgroundColor: ' #C70039 ', color: '#f0f7f1' }
    }
    
  }
