import React,{useEffect,useState} from 'react'
import Navegacion from '../paginainicial/Navegador';
import PiedePagina from '../paginainicial/Piedepagina';
import PropTypes from 'prop-types'
import Constantes from '../Constante';
import Paginate from '../Paginacion/Paginate';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './Estilo.css'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useHistory,
    useParams
  } from "react-router-dom";
function InformacionDeEnvios(props) {
    const {id} = useParams();
    const history = useHistory();
    const menuinicial = () =>{
      history.push("/Envio_Productos")
    }
    const [contado, SETCONTADOR]    = useState(0);
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                           EXTRAYENDO INFORMACIÓN DE LAS UNIDADES OPERATIVAS     
const [unidadoperativa, setUnidadOperativa] =   useState([]);
const SELECT_UNIDADOPERATIVA = () =>
{
    fetch(`${Constantes.RUTA_API8}/obtener-unidadoperativa.php`).then((res) =>
    res.json()).then((data)=>{
    setUnidadOperativa(data);
    }); 
       
}
    const [informacionenvio, setEnvios_info]           =   useState([]);
    const [envioid, setEnvio]           =   useState([]);
   
    const EXTRAYENDOINFORMACIONENVIO = () =>
    {
     // console.log(id)
      fetch(`${Constantes.RUTA_API8}/obtener-envio_id.php`, {
        method: "POST",
        body: id, 
        })
      .then((res) =>res.json())
      .then((data)=>{
        setEnvio(data);
        
      });
      const fechaactual           =   new Date();
      const estructuraFecha       =   fechaactual.getFullYear()+"-"+(fechaactual.getMonth()+1)+"-"+fechaactual.getDate()+" "+fechaactual.getHours()+":"+fechaactual.getMinutes()+":"+fechaactual.getSeconds();;
      document.getElementById("IDFECHACRECION").value = estructuraFecha;
  
    }
    const selecEnvioDetalle = () =>
    {
      const fechaceptacion           =   new Date();
      const estructuraFecha       =   fechaceptacion.getFullYear()+"-"+(fechaceptacion.getMonth()+1)+"-"+fechaceptacion.getDate()+" "+fechaceptacion.getHours()+":"+fechaceptacion.getMinutes()+":"+fechaceptacion.getSeconds();
      const objeto_envios     =
          {
              id                      :   id,
              fechactualizacion       :   estructuraFecha,
          }
          
          const pasando_objetoJSON = JSON.stringify(objeto_envios);
          console.log(pasando_objetoJSON);
       fetch(`${Constantes.RUTA_API8}/obtener-enviodetalle_info2.php`, { 
        method: "POST",
        body: pasando_objetoJSON, 
        })
      .then((res) =>res.json())
      .then((data)=>{
      
        setEnvios_info(data);
      });
    }
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                   EXTRAYENDO BODEGAS DE UNIDAD OPERATIVA 
const[bodegaunidadOpera, setBODEGAUNIDAD_OPERA] = useState([])
const FUNCION_EXTRAER_BODEGAS= () => 
 {  
    let idunidad = document.getElementById("uniDad_Operativa").value;
    // console.log(idunidad);
    fetch(`${Constantes.RUTA_API6}/obtener-bodegaunidadoperativa.php`, {
        method: "POST",
        body: idunidad, 
        })
    .then((res) =>res.json())
    .then((data)=>{
        setBODEGAUNIDAD_OPERA(data);
    });
 }
 //:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//                   EXTRAYENDO PRESENTACIONES DEL PRODUCTO SELECCIONADO 
const[cantidadinput, contestandoSolicitud] = useState(false)
var INPUT ;
const FUNCION_ENVIO_PRODUCTOS= () => 
{ 
  contestandoSolicitud(false); 
  let OBSERVACIONES       =   document.getElementById("observacion_De_envio").value;
  let FECHACREACION       =   document.getElementById("IDFECHACRECION").value;
  let unidad_operativo    =   document.getElementById("uniDad_Operativa").value;
  let BODEGA              =   document.getElementById("IDBODEGADESALIDA").value;
  if( unidad_operativo == "" || FECHACREACION == "" || BODEGA == ""  || cantidadinput == true)//VERIFICO SI LOS CAMPOS REQUERIDOS EXISTEN
  {   //NOTIFICO SI NO EXISTEN LOS DATOS :::::::::::::::::::::::::::::::::::::::::::::::::::
      toast.error('¡¡ ERROR !! INGRESAR DATOS NECESARIOS ', {
      position: "top-left",
      autoClose: 2000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      });
  }
  else
  {
    informacionenvio.map(data_enviodetalle =>
    {    
         INPUT               =   document.getElementById(data_enviodetalle.id).value;
        if(INPUT === "" || INPUT <= 0)
        {
          INPUT = document.getElementById(data_enviodetalle.id).placeholder;
         // console.log("soy placeholder"+INPUT);
        }
          const objeto_envios     =
          {
              id                      :   id,
              unidad_operativa        :   unidad_operativo,
              observacionesdeenvio    :   OBSERVACIONES,
              fecha_creacion          :   FECHACREACION,
              bodega                  :   BODEGA,
              id_productos            :   data_enviodetalle.id,
              valor_producto          :    INPUT,
          }
          const pasando_objetoJSON = JSON.stringify(objeto_envios);
         // console.log(pasando_objetoJSON);
          fetch(`${Constantes.RUTA_API8}/guardar_envio_de_productos.php`, {
          method: "POST",
          body:pasando_objetoJSON, 
          })
      })
      toast.success('PETICIÓN REGISTRADA CON EXITO ', {
        position: "top-left",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        });
  }
    
}
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
let valorinput ;
const Funcion_tomardatos =  (variable1,cantidad) => {
  valorinput = document.getElementById(variable1);
  if(valorinput != null )
  {
    valorinput.value = cantidad;
  }
 
}
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
    useEffect(() => {
        selecEnvioDetalle();
        SELECT_UNIDADOPERATIVA();
        FUNCION_EXTRAER_BODEGAS();
        EXTRAYENDOINFORMACIONENVIO();
    }, [contado])
    return (
        <>
         <Navegacion/>
         <div className="container tipoletra">
         <ToastContainer/>
            <div className="row justify-content-between">
            <div className="row">
            <div className="col">
              <button onClick={menuinicial}type="button" className="btn colormenutablas btn-sm espaciomenusuperior">
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-left-square-fill" viewBox="0 0 16 16">
              <path d="M16 14a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v12zm-4.5-6.5H5.707l2.147-2.146a.5.5 0 1 0-.708-.708l-3 3a.5.5 0 0 0 0 .708l3 3a.5.5 0 0 0 .708-.708L5.707 8.5H11.5a.5.5 0 0 0 0-1z"/>
              </svg> REGRESAR A ENVIOS
              </button>
              <br/><br/>
            </div>
            <div className='col-auto'> 
            </div>
            </div>
          <div  className="col-sm-3 colorfondobarra ">
          <div className='row tipoletra'>
          <div className="w-100">
                    <div className="form-group">
                    <label for="exampleFormControlTextarea1 " className='tipoletra2'>UNIDAD OPERATIVA DE PETICION</label>
                    <select className="form-control form-control-sm tipoletra" id="unidadpeticion_id" required disabled>
                    {envioid.map(data_unidadoperativa => 
                    {
                    return (
                    <option value={data_unidadoperativa.unidad_operativa_id_origen}>{data_unidadoperativa.nombreunidad}</option>
                    );
                    })}
                    </select>
                    </div> 
      </div>
      <div className="w-100 tipoletra">
                    <div className="form-group">
                    <label for="exampleFormControlTextarea1 " className='tipoletra2'>ESCRIBIR OBSERVACION</label>
                    <textarea className="form-control tipoletra" id="observacion_De_envio" rows="1" ></textarea>
                    </div>
      </div>
      <div className="col">
                    <div className="form-group">
                    <label for="exampleFormControlInput1" className="form-label tipoletra2">FECHA CREACIÓN: </label>
                    <input type="text" disabled className="form-control tipoletra" id="IDFECHACRECION"  required   placeholder="FECHA DE CREACION" />
                    </div>
      </div>
      <div className='w-100'>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-exclamation-circle-fill  coloricono" viewBox="0 0 16 16">
                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4zm.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2z"/>  
                    </svg>
                    <label for="exampleFormControlSelect1 " className='tipoletra2'> SELECCIONAR UNIDAD OPERATIVA   
                    </label>
                    <select className="form-control form-control-sm tipoletra" id="uniDad_Operativa" required onClick={FUNCION_EXTRAER_BODEGAS}>
                    {unidadoperativa.map(data_unidadoperativa => 
                    {
                      return (
                      <option value={data_unidadoperativa.id}>{data_unidadoperativa.nombre}</option>
                      );
                    })}
                    </select>
      </div>
      <div className='w-100'>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-exclamation-circle-fill coloricono" viewBox="0 0 16 16">
                    <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM8 4a.905.905 0 0 0-.9.995l.35 3.507a.552.552 0 0 0 1.1 0l.35-3.507A.905.905 0 0 0 8 4zm.002 6a1 1 0 1 0 0 2 1 1 0 0 0 0-2z"/>  
                    </svg>
                    <label for="exampleFormControlInput1" className="form-label tipoletra2">SELECCIONE BODEGA DE SALIDA :  </label>
                    <select className="form-control form-control-sm tipoletra" id="IDBODEGADESALIDA" required aria-label="Default select example">
                    {bodegaunidadOpera.map(data_bodega => 
                    {
                      return (
                      <option value={data_bodega.id} selected>{data_bodega.nombre}</option>
                      );
                    })}
                    </select>
      </div>
      <div className='w-100'>
        <br/>
                <button type="button" className="btn btn-success btn-sm tipoletra" onClick={FUNCION_ENVIO_PRODUCTOS}>
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-send-plus" viewBox="0 0 16 16">
                <path d="M15.964.686a.5.5 0 0 0-.65-.65L.767 5.855a.75.75 0 0 0-.124 1.329l4.995 3.178 1.531 2.406a.5.5 0 0 0 .844-.536L6.637 10.07l7.494-7.494-1.895 4.738a.5.5 0 1 0 .928.372l2.8-7Zm-2.54 1.183L5.93 9.363 1.591 6.602l11.833-4.733Z"/>
                <path d="M16 12.5a3.5 3.5 0 1 1-7 0 3.5 3.5 0 0 1 7 0Zm-3.5-2a.5.5 0 0 0-.5.5v1h-1a.5.5 0 0 0 0 1h1v1a.5.5 0 0 0 1 0v-1h1a.5.5 0 0 0 0-1h-1v-1a.5.5 0 0 0-.5-.5Z"/>
                </svg> ENVIAR SOLICITUD PARA ENVIO DE ARTICULOS
                </button>
      </div>
      </div>    
</div>
<div id=""  className="col-sm-9 ">
<div id="" className="row  tipoletra">
<div className="col">
                <button className="btn btn-danger tipoletra2" onClick={()=>SETCONTADOR(contado+1)} title="Actualizar Tabla" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-clockwise" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                </svg> CARGAR VALORES POR DEFECTO
                </button>
            </div>
        <div className="table-responsive-md ">      
            <table className="table table-hover  ">
            <thead>
            <tr>
                <th scope="col">#</th>
                <th scope="col"><span className="badge bg-light text-dark"> 
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-send-plus" viewBox="0 0 16 16">
                <path d="M15.964.686a.5.5 0 0 0-.65-.65L.767 5.855a.75.75 0 0 0-.124 1.329l4.995 3.178 1.531 2.406a.5.5 0 0 0 .844-.536L6.637 10.07l7.494-7.494-1.895 4.738a.5.5 0 1 0 .928.372l2.8-7Zm-2.54 1.183L5.93 9.363 1.591 6.602l11.833-4.733Z"/>
                <path d="M16 12.5a3.5 3.5 0 1 1-7 0 3.5 3.5 0 0 1 7 0Zm-3.5-2a.5.5 0 0 0-.5.5v1h-1a.5.5 0 0 0 0 1h1v1a.5.5 0 0 0 1 0v-1h1a.5.5 0 0 0 0-1h-1v-1a.5.5 0 0 0-.5-.5Z"/>
                </svg> PRODUCTO</span></th>
                <th scope="col">DESCRIPCION PRODUCTO</th>
                <th scope="col">CANTIDAD SOLICITADA</th>
                <th scope="col">OBSERVACION DEL PEDIDO</th>
                <th scope="col">DIGITAR LA CANTIDAD A ENVIAR</th>
               
                {/*<th scope="col">Eliminar</th>*/}
            </tr>
            </thead>
            <tbody>
            {informacionenvio.map(data_enviodetalle =>{
                return(
                    <tr>
                        <td>{data_enviodetalle.id}</td>
                        <td>{data_enviodetalle.nombreproducto}</td>
                        <td>{data_enviodetalle.descripcion_presentacion}</td>
                        <td>{data_enviodetalle.cantidad_pedida}</td>
                        <td onClick={Funcion_tomardatos(data_enviodetalle.id,data_enviodetalle.cantidad_pedida)}>{data_enviodetalle.observaciones_de_peticion}</td>
                        <td >
                          <input required className='mi-input' step="0.01" id={data_enviodetalle.id}  type="number" min="0"   placeholder={data_enviodetalle.cantidad_pedida} />
                        </td>
                    </tr>
                );
            })}
            </tbody>
            </table>
    </div>  
    </div>         
</div>
 
         
    </div>
    </div>
    <PiedePagina/>
        </>
    )
}

InformacionDeEnvios.propTypes = {

}

export default InformacionDeEnvios

