  function CAMBIO_LETRA_SERVICIO(ubicacion){
    //console.log("ubicacion "+ubicacion);
    var element = document.getElementById("SERVICIO");
    const valor = ubicacion;
  if(ubicacion == 1 && element != undefined){
    element.innerHTML = "SI";
    element.style.background = "#35854c";
        element.style.color = "white";
        element.style.borderRadius = "4px";
        element.style.padding = "2px"
  }
  else if(ubicacion == 0 && element != undefined){
        element.innerHTML = "NO";
        element.style.background = "#c70039";
        element.style.color = "white";
        element.style.borderRadius = "4px";
        element.style.padding = "2px"
  } //console.log("BUENAS TARDES CHELE"+id) 
  }
