import React,{useState,useEffect} from 'react'
import PropTypes from 'prop-types'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Constantes from '../Constante';
//:::::::::::::::::: INGRESANDO FECHA PARA PRODUCTOS :::::::::::::::::::::::::::::::::::::::::::
const ModalPRODUCTPRODUCTOREGISTRAR = () =>{
    //e.preventDefault();
    const fechaactual           =   new Date();
    const estructuraFecha       =   fechaactual.getFullYear()+"-"+(fechaactual.getMonth()+1)+"-"+fechaactual.getDate()+" "+fechaactual.getHours()+":"+fechaactual.getMinutes()+":"+fechaactual.getSeconds();;
    document.getElementById("FECHACREPRODUCTOmodal").value = estructuraFecha;
    document.getElementById("FECHAUPDPRODUCTO").value = estructuraFecha;
   }
function FormularioProveedor(props) {

//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::://
//                                                                          //
//              FUNCION PARA REGISTRAR DATOS DENTRO DE                      //
//                    SQL SERVER MEDIANTE MODALES                           //
//                                                                          //
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::: ::::::::::::://
//::::::::::::::::::: REGISTRANDO PROVEEDOR :::::::::::::::::::::::::::::::::::
const CREANDOPROVEEDOR = (e) =>
{
    e.preventDefault();
    const objetoenvioproveedor =
    {
        nombre_legal                    :   e.target[0].value,
        nombre_comercial                :   e.target[1].value,
        contacto                        :   e.target[2].value,
        direccion                       :   e.target[3].value,
        telefono1                       :   e.target[4].value,
        telefono2                       :   e.target[5].value,
        telefono3                       :   e.target[6].value,
        email                           :   e.target[7].value,
        web                             :   e.target[8].value,
        serieccf                        :   e.target[9].value,
        seriencr                        :   e.target[10].value,
        seriecrt                        :   e.target[11].value,
        seriefct                        :   e.target[12].value,
        nrc                             :   e.target[13].value,
        nit                             :   e.target[14].value,
        dui                             :   e.target[15].value,
        otrodocumento                   :   e.target[16].value,
        ubicacion                       :   e.target[17].value,
        grancontribuyente               :   e.target[18].value,
        fh_creacion                     :   e.target[19].value,
        fh_actualizacion                :   e.target[20].value,
    }
        const cargaUtil = JSON.stringify(objetoenvioproveedor);
        //console.log(cargaUtil);
       funcionregistrarproveedor(cargaUtil);
};
    /********* FUNCION DE CARGA PARA ENVASES ******** */
    const funcionregistrarproveedor=async(cargaUtil) => 
    {
        const respuesta = await fetch(`${Constantes.RUTA_API5}/guardar-proveedor.php`, 
        {
            method: "POST",
            body: cargaUtil,
        });
        const exitoso = await   respuesta.json();
        if (exitoso)
         {
        // console.log("entre a notificacion");
            toast.success('PROVEEDOR  REGISTRADO', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            });
        }
        else
        {
            toast.error('CODIGO EXISTENTE ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            });
        }
}

    return (
        <>

<div className="btn-group-vertical">
            <h6 className="colorletra"> Menú de opciones</h6>
            <button type="button" className="btn btn-light botonesmenulateral btn-sm"  onClick={()=>ModalPRODUCTPRODUCTOREGISTRAR()}           data-toggle="modal"  data-target   ="#MODALCREANDOPRODUCTO">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-journal-medical" viewBox="0 0 16 16">
            <path fill-rule="evenodd" d="M8 4a.5.5 0 0 1 .5.5v.634l.549-.317a.5.5 0 1 1 .5.866L9 6l.549.317a.5.5 0 1 1-.5.866L8.5 6.866V7.5a.5.5 0 0 1-1 0v-.634l-.549.317a.5.5 0 1 1-.5-.866L7 6l-.549-.317a.5.5 0 0 1 .5-.866l.549.317V4.5A.5.5 0 0 1 8 4zM5 9.5a.5.5 0 0 1 .5-.5h5a.5.5 0 0 1 0 1h-5a.5.5 0 0 1-.5-.5zm0 2a.5.5 0 0 1 .5-.5h5a.5.5 0 0 1 0 1h-5a.5.5 0 0 1-.5-.5z"/>
            <path d="M3 0h10a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H3a2 2 0 0 1-2-2v-1h1v1a1 1 0 0 0 1 1h10a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H3a1 1 0 0 0-1 1v1H1V2a2 2 0 0 1 2-2z"/>
            <path d="M1 5v-.5a.5.5 0 0 1 1 0V5h.5a.5.5 0 0 1 0 1h-2a.5.5 0 0 1 0-1H1zm0 3v-.5a.5.5 0 0 1 1 0V8h.5a.5.5 0 0 1 0 1h-2a.5.5 0 0 1 0-1H1zm0 3v-.5a.5.5 0 0 1 1 0v.5h.5a.5.5 0 0 1 0 1h-2a.5.5 0 0 1 0-1H1z"/>
            </svg>  CREAR PROVEEDOR</button>
           
                <button type="button" className="btn btn-light botonesmenulateral">.....</button>
            </div>


             {/* :::::::::::::::::::: MODAL CREAR PRODUCTO  :::::::::::::::::::::::::::::::::::::::::::  */}

             <div className="modal fade" id="MODALCREANDOPRODUCTO" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog">
            <div className="modal-content">
            <div className="container py-4">
                
            

            <div className="card-header">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-pen" viewBox="0 0 16 16">
            <path d="m13.498.795.149-.149a1.207 1.207 0 1 1 1.707 1.708l-.149.148a1.5 1.5 0 0 1-.059 2.059L4.854 14.854a.5.5 0 0 1-.233.131l-4 1a.5.5 0 0 1-.606-.606l1-4a.5.5 0 0 1 .131-.232l9.642-9.642a.5.5 0 0 0-.642.056L6.854 4.854a.5.5 0 1 1-.708-.708L9.44.854A1.5 1.5 0 0 1 11.5.796a1.5 1.5 0 0 1 1.998-.001zm-.644.766a.5.5 0 0 0-.707 0L1.95 11.756l-.764 3.057 3.057-.764L14.44 3.854a.5.5 0 0 0 0-.708l-1.585-1.585z"/>
            </svg> CREANDO PROVEEDOR
            </div>

            <form className="field"  onSubmit={CREANDOPROVEEDOR}>

            <div className="ex3">
            <div className="card-header text-center">
                INFORMACIÓN GENERAL DEL PROVEEDOR
            </div>
            
            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">NOMBRE LEGAL : </label>
            <input type="text" className="form-control" id="NOMBREPRECIOLISTA"  required maxLength="100"   placeholder="INGRESE NOMBRE LEGAL DEL PROVEEDOR" />
            </div>


            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">NOMBRE COMERCIAL: </label>
            <input type="text" className="form-control" id="IDDESCRIPCIONLISTA"  required  maxLength="100" placeholder="INGRESE NOMBRE COMERCIAL DEL PROVEEDOR"/>
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">NOMBRE CONTACTO: </label>
            <input type="text" className="form-control" id="IDDESCRIPCIONLISTA"  required  maxLength="100" placeholder="INGRESE NOMBRE CONTACTO"/>
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">DIRECCIÓN: </label>
            <input type="text" className="form-control" id="IDDESCRIPCIONLISTA"  required  maxLength="300" placeholder="INGRESE DIRECCION DEL PROVEEDOR"/>
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">TELEFONO 1: </label>
            <input type="text" className="form-control" id="MARGENLISTA" maxLength="20"      placeholder="INGRESE TELEFONO 1" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">TELEFONO 2: </label>
            <input type="text" className="form-control" id="MARGENLISTA"  maxLength="20"      placeholder="INGRESE TELEFONO 2" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">TELEFONO 3: </label>
            <input type="text" className="form-control" id="MARGENLISTA"  maxLength="20"     placeholder="INGRESE TELEFONO 3" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">EMAIL: </label>
            <input type="text" className="form-control" id="MARGENLISTA"  maxLength="150"  required    placeholder="INGRESE CORREO ELECTRONICO" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">SITIO WEB: </label>
            <input type="text" className="form-control" id="MARGENLISTA"  maxLength="150"  required    placeholder="INGRESE SITIO WEB" />
            </div>
            <br/>
            
            <div className="card-header text-center">
                REGISTRANDO SERIES LEGALES DEL PROVEEDOR
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">SERIE CREDITO FISCAL : </label>
            <input type="text" className="form-control" id="CCF"   maxLength="10"   placeholder="INGRESE SERIE CREDITO FISCAL" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">SERIE NOTA DE CREDITO : </label>
            <input type="text" className="form-control" id="NCR"   maxLength="10"   placeholder="INGRESE SERIE NOTA DE CREDITO" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">SERIE COMPROBANTE DE RETENCIÓN : </label>
            <input type="text" className="form-control" id="CRT"   maxLength="10"   placeholder="INGRESE SERIE COMPROBANTE DE RETENCIÓN" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">SERIE DE CONSUMIDOR : </label>
            <input type="text" className="form-control" id="FCT"   maxLength="10"   placeholder="INGRESE SERIE DE CONSUMIDOR" />
            </div>
            <br/>
            <div className="card-header text-center">
                REGISTRANDO INFORMACIÓN LEGAL
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">NUMERO DE REGISTRO DE CONTRIBUYENTE IVA: </label>
            <input type="text" className="form-control" id="MARGENLISTA"  maxLength="10"      placeholder="INGRESE NRC" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">NIT: </label>
            <input type="text" className="form-control" id="MARGENLISTA"  maxLength="17"      placeholder="INGRESE NIT" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">DUI: </label>
            <input type="text" className="form-control" id="MARGENLISTA"  maxLength="12"      placeholder="INGRESE DUI" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">OTRO DOCUMENTO : </label>
            <input type="text" className="form-control" id="MARGENLISTA"  maxLength="20"      placeholder="INGRESE OTRO DOCUMENTO" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">UBICACION DEL PROVEEDOR: </label>
            <div className="custom-control custom-switch">
            <select className="form-select" aria-label="Default select example">
                <option  value="" disabled select>SELECCIONE</option>
                <option  value="1">LOCAL</option>
                <option value="2">EXTRANJERO</option>
            </select>
            </div>
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" className="form-label">GRAN CONTRIBUYENTE: </label>
            <div className="custom-control custom-switch">
            <select className="form-select" aria-label="Default select example">
                <option  value="" disabled select>SELECCIONE</option>
                <option  value="1">SI</option>
                <option value="0">NO</option>
            </select>
            </div>
            </div>

           

            
            
            <div className="mb-2">
            <label for="exampleFormControlTextarea1" className="form-label">FECHA CREACIÓN: </label>
            <input type="text" className="form-control" disabled  id="FECHACREPRODUCTOmodal"    />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlTextarea1" className="form-label">FECHA ACTUALIZACIÓN: </label>
            <input type="text" className="form-control" disabled  id="FECHAUPDPRODUCTO"   placeholder />
            </div>

           

            <div className="modal-footer">
            <button className="btn btn-primary is-success mt-2" >REGISTRAR</button>
            <button className="btn btn-danger is-success mt-2 close"data-dismiss="modal" aria-label="Close" >CERRAR</button>
            </div>
            </div>
            </form>
            </div>
            </div>
            </div>
            </div>

        </>
    )
}

FormularioProveedor.propTypes = {

}

export default FormularioProveedor

