import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types'
import './paginacion.css';

function Paginate({paginaActual,TotalPagina,paginacion}) {
    const pageNumbers   =   [];
    //console.log("Totl de registros :"+TotalPagina);
    for(let i=1;i<=Math.ceil(TotalPagina/paginaActual);i++){
        pageNumbers.push(i);
       // console.log("i :"+i);
    }
    
    return (
        <>
                <nav>
                
                    <ul className="pagination">
                        {pageNumbers.map(number =>(
                            //console.log("NUMERO:"+number),
                           
                            <li key={number} >
                                <a onClick={() =>paginacion(number)}  className="btn btn-secondary btn-sm margenbotonpaginacion">
                                    {number}
                                </a>
                            </li>
                        ))}
                    </ul>
                    
                </nav>
        </>
    )
}

Paginate.propTypes = {

}

export default Paginate

