const indexPrimeraPagina    =   indexUltimaPagina   -   cantidadPagina;
const publicacionActual     =   productogeneral.slice(indexPrimeraPagina,indexUltimaPagina);

const paginacion              =   pageNumber => setPaginaActual(pageNumber);
