import React from 'react'
import PropTypes from 'prop-types'
import Navegacion from '../paginainicial/Navegador';
import PiedePagina from '../paginainicial/Piedepagina';
import descripcionproducto from '../../img/descripcion-del-producto.png';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useHistory,
    useParams
  } from "react-router-dom";
function BodegaIndividualinformacion(props) {
    const {id} = useParams();
    const history = useHistory();
  const menuinicial = () =>{
    history.push("/Inventario")
  }
    return (
        <>
            <Navegacion/>
            <div class="container">
        <div class="row">
            <div class="col-sm">
            <button onClick={menuinicial}type="button" className="btn colormenutablas btn-sm espaciomenusuperior">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-left-square-fill" viewBox="0 0 16 16">
            <path d="M16 14a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V2a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v12zm-4.5-6.5H5.707l2.147-2.146a.5.5 0 1 0-.708-.708l-3 3a.5.5 0 0 0 0 .708l3 3a.5.5 0 0 0 .708-.708L5.707 8.5H11.5a.5.5 0 0 0 0-1z"/>
            </svg> REGRESAR A INVENTARIO 
            </button>
            </div>
            <div className="col-sm SUBRAYANDO_TEXTO ">
                   INFORMACIÓN DETALLADA DE BODEGA
            </div>
            <div class="col-sm">
            <button type="button" className="btn color_actualizar_informacion  btn-sm espaciomenusuperior" onClick     data-toggle="modal"  data-target   ="#MODALCOMPRADETALLE">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-repeat" viewBox="0 0 16 16">
            <path d="M11.534 7h3.932a.25.25 0 0 1 .192.41l-1.966 2.36a.25.25 0 0 1-.384 0l-1.966-2.36a.25.25 0 0 1 .192-.41zm-11 2h3.932a.25.25 0 0 0 .192-.41L2.692 6.23a.25.25 0 0 0-.384 0L.342 8.59A.25.25 0 0 0 .534 9z"/>
            <path fill-rule="evenodd" d="M8 3c-1.552 0-2.94.707-3.857 1.818a.5.5 0 1 1-.771-.636A6.002 6.002 0 0 1 13.917 7H12.9A5.002 5.002 0 0 0 8 3zM3.1 9a5.002 5.002 0 0 0 8.757 2.182.5.5 0 1 1 .771.636A6.002 6.002 0 0 1 2.083 9H3.1z"/>
            </svg> ACTUALIZAR INFORMACIÓN</button>
            </div>
            </div>
    </div>
    <br/>
    <br/>


    
    <div className="container col-sm-12">
            <div id="RPRODUCTO" class="row ">
               <div  class="col-sm-2 ">
               <figure>
                <img src={descripcionproducto}className="card-img-top  imagen_informacion" title="Mi Inventario" alt="Imagen Inventario"/>  
               <figcaption>
               
               </figcaption>
                </figure>
               </div>
               
            <div id="IDTABLAPRODUCTO"  class="col-sm-10 ">
                <nav>
                <div class="nav nav-tabs" id="nav-tab" role="tablist">
                    <a class="nav-link active" id="nav-home-tab" data-toggle="tab" href="#nav-home" role="tab" aria-controls="nav-home" aria-selected="true">PRODUCTOS ALOJADOS</a>
                    <a class="nav-link" id="nav-profile-tab" data-toggle="tab" href="#nav-profile" role="tab" aria-controls="nav-profile" aria-selected="false">INFORMACIÓN DE BODEGA</a>
                   </div>
                </nav>
                <div class="tab-content" id="nav-tabContent">
                    <div class="tab-pane fade show active" id="nav-home" role="tabpanel" aria-labelledby="nav-home-tab">
                    
                    <table class="table table-sm tipoletra table-bordered table-hover">
                    <thead>
                        <th>PRODUCTO</th>
                        <th>PRESENTACION</th>
                        <th>CANTIDAD</th>
                    </thead>
                    <tbody>
                        <tr>
                        <td>PRODUCTO 1</td>
                        <td>PRESENTACION 1</td> 
                        <td>12</td> 
                        </tr>
                        <tr>
                        <td>PRODUCTO 2</td>
                        <td>PRESENTACION 2</td> 
                        <td>6</td> 
                        </tr>
                    </tbody>
                    </table>
                    </div>
                    <div class="tab-pane fade" id="nav-profile" role="tabpanel" aria-labelledby="nav-profile-tab">
                    
                    <table class="table table-sm tipoletra table-bordered table-hover">
                    <tbody>
                   </tbody>
                     </table>
                    </div>
                    <div class="tab-pane fade" id="nav-contact" role="tabpanel" aria-labelledby="nav-contact-tab">
                    <table class="table table-sm tipoletra table-bordered table-hover">
                    <tbody>
                   
                    </tbody>
                    </table>
                      
                      </div>
                </div> 
            </div>
            </div>
            </div>
            <PiedePagina/>  
        </>
    )
}

BodegaIndividualinformacion.propTypes = {

}

export default BodegaIndividualinformacion

