import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types'

import Constantes from '../Constante';
import Paginate from '../Paginacion/Paginate';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Loading from '../Loading';
import LoadingModal from '../LoadingModal';
import inventario from '../../img/inventario.png';
import './Inventario.css';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useHistory,
    useParams
  } from "react-router-dom";
function Bodegas(props) {
    const [bodegas, setBODEGAS]           =   useState([]);
    const [tablabodeg, setTablaBodegas]             =   useState([]);
    const [busqueda, setBuscar]         =   useState([]);
    const [actualizar, setActualizar]   =   useState(0);
   
    const [lodingg, setLodingg] = useState(false)
    const [lodinggModal, setLodinggModal] = useState(false)
    //:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::://
//                  CODIGO PARA PAGINACION CREACION DE ESTADOS                                  //  

const [paginaActual,setPaginaActual]    =   useState(1);
const [cantidadPagina]                  =   useState(100);

const indexUltimaPagina     =   paginaActual*cantidadPagina;
const indexPrimeraPagina    =   indexUltimaPagina   -   cantidadPagina;
const publicacionActual     =   bodegas.slice(indexPrimeraPagina,indexUltimaPagina);

const paginacion              =   pageNumber => setPaginaActual(pageNumber);
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::://

//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::://
//                                                                                                  //
//                         FUNCIONES PARA CRUD DE BODEGAS                                           //
//                                                                                                  //
//                                                                                                  //
//::::::::::::::: FUNCION PARA SELECCIONAR LOS DATOS DE LA TABLA ::::::::::::::::::::::::::::::::::://
const SELECTBODEGAS = () =>
{
    setLodingg(true)
    fetch(`${Constantes.RUTA_API3}/obtenerbodegas.php`).then((res) =>
    res.json()).then((data)=>{
    setBODEGAS(data);
    setTablaBodegas(data);
   // console.log(data);
    setLodingg(false)
});    
}
//:::::::::::::::::::::::::::::::::  FUNCION PARA BUSCADOR ::::::::::::::::::::::::::::::::::::::::::::::::::::::
const BuscarBodegas = (e) =>
{
    let valor           = document.getElementById("BuscarBodegaiNVEN").value;
    e.preventDefault();
    var codigo          = e.which || e.keyCode;
    if (codigo == 127){
    }
    if(codigo >= 32 && codigo <= 126)
    {
       // const bandera   =   1;  
       // console.log(String.fromCharCode(codigo));
        valor           = valor + String.fromCharCode(codigo);
    }
      document.getElementById("BuscarBodegaiNVEN").value = valor
      filtrar(document.getElementById("BuscarBodegaiNVEN").value);
}
//:::::::::::::::::::::::::::::::FUNCION FILTRAR::::::::::::::::::::::::::::::::::::::::::::::::::::::
const filtrar = (busqueda) => 
{
    const resultadoBusqueda = tablabodeg.filter((elementos) =>
    {
        if(elementos.divisionnombre.toLowerCase().toString().includes(busqueda)||elementos.nombre.toLowerCase().toString().includes(busqueda))
        {
            return elementos;
        }
    });
    setBuscar(resultadoBusqueda);
    setBODEGAS(resultadoBusqueda);
}

useEffect(() => {
    SELECTBODEGAS();
}, [actualizar])

    return (
        <>
            <div id="" className="row ">
            <div className="col">
                <button className="btn btn-info" onClick={()=>setActualizar(actualizar+1)} title="Actualizar Tabla" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-clockwise" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                </svg>
                </button>
            </div>
            <div className="col">
                <div className="input-group mb-3 mb-6 tamañodelbuscador ">    
                <input type="text"  onKeyPress={BuscarBodegas} className="form-control" id="BuscarBodegaiNVEN" aria-describedby="basic-addon2"   placeholder="Digite para buscar" title="Digitar para buscar..." />
                <span className="input-group-text" id="basic-addon2" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-keyboard" viewBox="0 0 16 16">
                <path d="M14 5a1 1 0 0 1 1 1v5a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V6a1 1 0 0 1 1-1h12zM2 4a2 2 0 0 0-2 2v5a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V6a2 2 0 0 0-2-2H2z"/>
                <path d="M13 10.25a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm0-2a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-5 0A.25.25 0 0 1 8.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 8 8.75v-.5zm2 0a.25.25 0 0 1 .25-.25h1.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-1.5a.25.25 0 0 1-.25-.25v-.5zm1 2a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-5-2A.25.25 0 0 1 6.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 6 8.75v-.5zm-2 0A.25.25 0 0 1 4.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 4 8.75v-.5zm-2 0A.25.25 0 0 1 2.25 8h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 2 8.75v-.5zm11-2a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-2 0a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm-2 0A.25.25 0 0 1 9.25 6h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 9 6.75v-.5zm-2 0A.25.25 0 0 1 7.25 6h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 7 6.75v-.5zm-2 0A.25.25 0 0 1 5.25 6h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5A.25.25 0 0 1 5 6.75v-.5zm-3 0A.25.25 0 0 1 2.25 6h1.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-1.5A.25.25 0 0 1 2 6.75v-.5zm0 4a.25.25 0 0 1 .25-.25h.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-.5a.25.25 0 0 1-.25-.25v-.5zm2 0a.25.25 0 0 1 .25-.25h5.5a.25.25 0 0 1 .25.25v.5a.25.25 0 0 1-.25.25h-5.5a.25.25 0 0 1-.25-.25v-.5z"/>
                </svg>
                </span>
                </div>
            </div>
        </div>
        <table class="table table-bordered table-hover table-sm tipoletra">
  <thead>
            <tr>
            <th scope="col">#</th>
            <th scope="col">IMAGEN</th>
            <th scope="col">BODEGA</th>
            <th scope="col">UNIDAD OPERATIVA</th>
            <th scope="col">OPCIONES</th>
    </tr>
  </thead>
  <tbody>
  {lodingg && <Loading/> }
            {publicacionActual.map(framework=>{
          return(
    <tr>
            <th scope="row"> 
                {framework.id}
            </th>
            <td> 
            <img src={inventario}className="card-img-top  imagentabla" title="Mi Inventario" alt="Imagen Inventario"/>
           </td>
            <td>{framework.nombre}</td>
            <td>{framework.divisionnombre}</td>
            <td>
            <Link to={`/Productos_Bodega/${framework.id}`}  className="btn btn-primary btn-sm" >
                   VER INVENTARIO
                </Link>
            </td>
    </tr>
     );
    })}
  </tbody>
</table>
<div className="ex4">
    <Paginate paginaActual={cantidadPagina} TotalPagina={bodegas.length} paginacion={paginacion}/>
    </div>
        </>
    )
}

Bodegas.propTypes = {

}

export default Bodegas

