    /********* FUNCION DE CARGA PARA ENVASES ******** */
const funcionregistroDivisones=async(cargaUtil) => 
{
    const respuesta = await fetch(`${Constantes.RUTA_API2}/guardar-division.php`, 
    {
        method: "POST",
        body: cargaUtil,
    });
    const exitoso = await   respuesta.json();
    if (exitoso) 
    {
        toast.success('DIVISION REGISTRADA ', {
        position: "top-left",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        });
        setActualizacionEstado(estadodeactualizacion+1);
    }else
    {
        toast.error('NO SE  REGISTRO ', {
        position: "top-left",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        });
    }
}
