const validarinputnumero = (e) =>
{
    let valor           = document.getElementById("CORRELATIVOFACTURAINICIO").value;
    e.preventDefault();
    var codigo          = e.which || e.keyCode;
   
    if(codigo >= 48 && codigo <= 57 )
    {
        
     //   console.log(String.fromCharCode(codigo));
        valor           = valor + String.fromCharCode(codigo);
    }
    
      document.getElementById("CORRELATIVOFACTURAINICIO").value = valor
}
