const Buscar2 = (e) =>
{
    let valor           = document.getElementById("Buscar2").value;
    e.preventDefault();
    var codigo          = e.which || e.keyCode;
    if (codigo == 127){
    }
    if(codigo >= 32 && codigo <= 126)
    {
        const bandera   =   1;  
       // console.log(String.fromCharCode(codigo));
        valor           = valor + String.fromCharCode(codigo);
    }
      document.getElementById("Buscar2").value = valor
      filtrar2(document.getElementById("Buscar2").value);
}
//:::::::::::::::::::::::::::::::FUNCION FILTRAR::::::::::::::::::::::::::::::::::::::::::::::::::::::
const filtrar2 = (busqueda) => 
{
    const resultadoBusqueda = tablaoper.filter((elementos) =>
    {
        if(elementos.divisionnombre.toLowerCase().toString().includes(busqueda)||elementos.nombre.toLowerCase().toString().includes(busqueda)||elementos.telefono_primario.toString().includes(busqueda)||elementos.telefono_secundario.toString().includes(busqueda)||elementos.email.toLowerCase().toString().includes(busqueda)||elementos.numero_registro_comercial.toString().includes(busqueda)||elementos.numero_mh.toString().includes(busqueda)||elementos.serie_factura.toString().includes(busqueda)||elementos.serie_ccf.toString().includes(busqueda)||elementos.correlativo_factura_inicio.toString().includes(busqueda)||elementos.correlativo_factura_fin.toString().includes(busqueda)||elementos.correlativo_ccf_inicio.toString().includes(busqueda)||elementos.correlativo_ccf_fin.toString().includes(busqueda)||elementos.direccion1.toLowerCase().toString().includes(busqueda)||elementos.direccion2.toLowerCase().toString().includes(busqueda)||elementos.direccion3.toLowerCase().toString().includes(busqueda)||elementos.serie_tiquete.toLowerCase().toString().includes(busqueda))
        {
            //console.log("ELEMENTOS: "+elementos);
            return elementos;
        }
    });
    setBuscar2(resultadoBusqueda);
    setUniOperativa(resultadoBusqueda);
}
