const getTableColor = criterio => {
    //criterio = criterio.toLowerCase()
    if(criterio == 1)
    {
        return { backgroundColor: '#DAF7A6 ', color: '#2e2c2c' }
    }
    else if(criterio == 0){
        return { backgroundColor: '  #e87272  ', color: '#2e2c2c' }
    } 
  }

