import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types'
import Navegacion from '../paginainicial/Navegador';
import PiedePagina from '../paginainicial/Piedepagina';
import Constantes from '../Constante';
import BodegasInventario from './BodegasInventario';
import InventarioGeneral from './InventarioGeneral';

function BodyInventarioGeneral(props) {
    const [banderalista,setLista]                           =   useState(false);
    //const [banderlistadetalle,setListaDetalle]              =   useState(false);
    //const [banderacombo,setCombo]                           =   useState(false);
    //const [banderacombodetalle,setCombodetalle]             =   useState(false);
    const [contadorefecto,setContadorEfecto]                =   useState(0);
//::::::::::::::::::::::::: Funcion habilitar TABLA COMPRAS ::::::::::::::::::::::::
const BODEGASINVENTARIO= e => 
{
    e.preventDefault();
    setLista(true);
    if(banderalista)
    {
        const compratabla                        = document.getElementById("RPRODUCTO");
        compratabla.style.display                = "";
        const compratabladetalle                 = document.getElementById("IDCOMPRA");
        compratabladetalle.style.display         = "none";
        setLista(false);
    }
    setContadorEfecto(contadorefecto +1);
}
 //::::::::::::::::::::::::: Funcion habilitar TABLA COMPRAS DETALLE ::::::::::::::::::::::::
const PRODUCTOSGENERALES = e => 
{
   e.preventDefault();
   setLista(true);
   if(banderalista)
   {
       const compratabla                         = document.getElementById("RPRODUCTO");
        compratabla.style.display                = "none";
        const compratabladetalle                 = document.getElementById("IDCOMPRA");
        compratabladetalle.style.display         = "";
       setLista(false);
   }
   setContadorEfecto(contadorefecto +1);
}
useEffect(() => {
    if(contadorefecto == 0)
        {
            const compratabla                         = document.getElementById("RPRODUCTO");
             compratabla.style.display                = "";
             const compratabladetalle                 = document.getElementById("IDCOMPRA");
             compratabladetalle.style.display         = "none";
        }
}, [contadorefecto])
    return (
        <>
            <Navegacion/>
            <div className="container col-sm-12">

        
            <ul className="nav justify-content-center texto-titulo  separacionmenusuperior">
            <li className="nav-item">
                <button className="btn colormenutablas btn-sm espaciomenusuperior"  onClick={BODEGASINVENTARIO} title="TABLA PRODUCTOS GENERALES" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-file-ruled" viewBox="0 0 16 16">
                <path d="M2 2a2 2 0 0 1 2-2h8a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2zm2-1a1 1 0 0 0-1 1v4h10V2a1 1 0 0 0-1-1H4zm9 6H6v2h7V7zm0 3H6v2h7v-2zm0 3H6v2h6a1 1 0 0 0 1-1v-1zm-8 2v-2H3v1a1 1 0 0 0 1 1h1zm-2-3h2v-2H3v2zm0-3h2V7H3v2z"/>
                </svg>  Bodegas
                </button>
            </li>

           
               <li className="nav-item">
                <button className="btn colormenutablas btn-sm espaciomenusuperior"  onClick={PRODUCTOSGENERALES} title="TABLA PRODUCTOS GENERALES" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-file-ruled" viewBox="0 0 16 16">
                <path d="M2 2a2 2 0 0 1 2-2h8a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H4a2 2 0 0 1-2-2V2zm2-1a1 1 0 0 0-1 1v4h10V2a1 1 0 0 0-1-1H4zm9 6H6v2h7V7zm0 3H6v2h7v-2zm0 3H6v2h6a1 1 0 0 0 1-1v-1zm-8 2v-2H3v1a1 1 0 0 0 1 1h1zm-2-3h2v-2H3v2zm0-3h2V7H3v2z"/>
                </svg> PRODUCTOS
                </button>
            </li>
            <li className="nav-item">
                <a className="nav-link disabled"title="Otras Opciones">Disabled</a>
            </li>
            </ul>
            <div id="RPRODUCTO" class="col-sm-12 ">
            
                <BodegasInventario/>
           
                </div>
            <div id="IDCOMPRA"  class="col-sm-12 ">
               <InventarioGeneral/>
            </div>
           
        

           
            </div>
            <PiedePagina/>  
        </>
    )
}

BodyInventarioGeneral.propTypes = {

}

export default BodyInventarioGeneral

