import React,{useEffect,useState} from 'react'
import PropTypes from 'prop-types'
import Constantes from '../Constante';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function FormularioBodeja(props) {
    //:::::::::::::::::: Ingreso de Bodegas :::::::::::::::::::::::::::::::::::::::::::
    const ModalBodegasRegistro = () =>{
    //e.preventDefault();
    const fechaactual           =   new Date();
    const estructuraFecha       =   fechaactual.getFullYear()+"-"+(fechaactual.getMonth()+1)+"-"+fechaactual.getDate()+" "+fechaactual.getHours()+":"+fechaactual.getMinutes()+":"+fechaactual.getSeconds();;
    document.getElementById("FECHACREbodegas").value = estructuraFecha;
    document.getElementById("FECHAUPDbodega").value = estructuraFecha;
   }
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::://
//                                                                          //
//              FUNCION PARA REGISTRAR DATOS DENTRO DE                      //
//                    SQL SERVER MEDIANTE MODALES                           //
//                                                                          //
//:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::: ::::::::::::://
//::::::::::::::::::: REGISTRANDO ENVASES :::::::::::::::::::::::::::::::::::
const CREANDOBODEGAS = (e) =>
{
    e.preventDefault();
    const objetoenviobodegas =
    {
        nombre                  :   e.target[0].value,
        unidad_operativa_id     :   e.target[1].value,
        habilitado              :   e.target[2].value,
        fh_creacion             :   e.target[3].value,
        fh_actualizacion        :   e.target[4].value
    }
        const cargaUtil = JSON.stringify(objetoenviobodegas);
        console.log(objetoenviobodegas);
       funcionregistrobodegas(cargaUtil);
};
    /********* FUNCION DE CARGA PARA ENVASES ******** */
const funcionregistrobodegas=async(cargaUtil) => 
{
    const respuesta = await fetch(`${Constantes.RUTA_API3}/guardar-bodega.php`, 
    {
    method: "POST",
    body: cargaUtil,
    });
    const exitoso = await   respuesta.json();
if (exitoso) {
   // console.log("entre a notificacion");
toast.success('BODEGA REGISTRADA ', {
    position: "top-left",
    autoClose: 2000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
});

}else
{
toast.error('NO SE  REGISTRO ', {
    position: "top-left",
    autoClose: 2000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
});}
}

//:::::::::::: TOMANDO LAS UNIDADES-DE-MEDIDA ::::::::::::::::::::::::::::::::::
const [unidadmedidaoperativa, setUnidadoperativa]           =   useState([]);
const cargandoinformacionUnidad = () =>
{
    fetch(`${Constantes.RUTA_API3}/obtenerunidadoperativahabilitadas.php`)
    .then((res) =>res.json())
    .then((data)=>{
        setUnidadoperativa(data);
    });  
}
const [actualizarinformacion, setActualizarinfo]    = useState(0);
useEffect(() => {
    cargandoinformacionUnidad();
}, [actualizarinformacion])
    return (
        <>
            <div class="btn-group-vertical">
            <h6 className="colorletra">Menú de opciones</h6>
            <div id="" className="row  ">
            <div className="col tipoletra botonesmenulateral">
                <button className="btn btn-primary btn-sm" onClick={()=>setActualizarinfo(actualizarinformacion+1)} title="Actualizar Tabla" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-clockwise" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                </svg>
                Actualizar Información
                </button>
            </div>
            </div>
                <button type="button" class="btn btn-sm btn-light botonesmenulateral"  onClick={()=>ModalBodegasRegistro()}     data-toggle="modal" data-target   ="#MODALBODEGAS">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-shop-window" viewBox="0 0 16 16">
                <path d="M2.97 1.35A1 1 0 0 1 3.73 1h8.54a1 1 0 0 1 .76.35l2.609 3.044A1.5 1.5 0 0 1 16 5.37v.255a2.375 2.375 0 0 1-4.25 1.458A2.371 2.371 0 0 1 9.875 8 2.37 2.37 0 0 1 8 7.083 2.37 2.37 0 0 1 6.125 8a2.37 2.37 0 0 1-1.875-.917A2.375 2.375 0 0 1 0 5.625V5.37a1.5 1.5 0 0 1 .361-.976l2.61-3.045zm1.78 4.275a1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0 1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0 1.375 1.375 0 1 0 2.75 0V5.37a.5.5 0 0 0-.12-.325L12.27 2H3.73L1.12 5.045A.5.5 0 0 0 1 5.37v.255a1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0zM1.5 8.5A.5.5 0 0 1 2 9v6h12V9a.5.5 0 0 1 1 0v6h.5a.5.5 0 0 1 0 1H.5a.5.5 0 0 1 0-1H1V9a.5.5 0 0 1 .5-.5zm2 .5a.5.5 0 0 1 .5.5V13h8V9.5a.5.5 0 0 1 1 0V13a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V9.5a.5.5 0 0 1 .5-.5z"/>
                </svg>     CREACIÓN DE BODEGAS</button>
                <button type="button" class="btn btn-light botonesmenulateral">.....</button>
            </div>

            {/* :::::::::::::::::::: MODAL ENVASE :::::::::::::::::::::::::::::::::::::::::::  */}
               
            <div class="modal fade" id="MODALBODEGAS" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog">
            <div class="modal-content">
            <div className="container py-4">
                
            

            <div className="card-header">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pen" viewBox="0 0 16 16">
            <path d="m13.498.795.149-.149a1.207 1.207 0 1 1 1.707 1.708l-.149.148a1.5 1.5 0 0 1-.059 2.059L4.854 14.854a.5.5 0 0 1-.233.131l-4 1a.5.5 0 0 1-.606-.606l1-4a.5.5 0 0 1 .131-.232l9.642-9.642a.5.5 0 0 0-.642.056L6.854 4.854a.5.5 0 1 1-.708-.708L9.44.854A1.5 1.5 0 0 1 11.5.796a1.5 1.5 0 0 1 1.998-.001zm-.644.766a.5.5 0 0 0-.707 0L1.95 11.756l-.764 3.057 3.057-.764L14.44 3.854a.5.5 0 0 0 0-.708l-1.585-1.585z"/>
            </svg> CREANDO BODEGAS
            </div>

            <form className="field"  onSubmit={CREANDOBODEGAS}>
            
            <div className="mb-2">
            <label for="exampleFormControlInput1" class="form-label">NOMBRE BODEGA: </label>
            <input type="text" class="form-control" id="NOMBREBODEGA"  required maxLength="500"  placeholder="INGRESE BODEGA" />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlInput1" class="form-label">UNIDAD OPERATIVA: </label>
            <select class="form-select" aria-label="Default select example">
            {unidadmedidaoperativa.map(framework=>{
                 return(
                <option value={framework.id}>{framework.nombre}</option>
                );
            })}
            </select>
            </div>

            <div className="mb-2">
            <label for="exampleFormControlTextarea1" className="form-label">HABILITADO: </label>
            <input type="text" class="form-control" disabled value="1" id="HABILITADO"   placeholder />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlTextarea1" className="form-label">FECHA CREACIÓN: </label>
            <input type="text" class="form-control" disabled  id="FECHACREbodegas"    />
            </div>

            <div className="mb-2">
            <label for="exampleFormControlTextarea1" className="form-label">FECHA ACTUALIZACIÓN: </label>
            <input type="text" class="form-control" disabled  id="FECHAUPDbodega"   placeholder />
            </div>
            <div class="modal-footer">
            <button className="btn btn-primary is-success mt-2" >REGISTRAR</button>
            <button className="btn btn-danger is-success mt-2 close"data-dismiss="modal" aria-label="Close" >CERRAR</button>
            </div>
            </form>
            </div>
            </div>
            </div>
            </div>

        </>
    )
}

FormularioBodeja.propTypes = {

}

export default FormularioBodeja

