//::::::::::::::: FUNCION PARA SELECCIONAR LOS DATOS DE LA TABLA ::::::::::::::::::::::::::::::::::://
const SELECTPRODUCTOGENERAL = () =>
{
    setLodingg(true); 
    fetch(`${Constantes.RUTA_API7}/obtener-todoslosproductos.php`).then((res) =>
    res.json()).then((data)=>{
    setPRODUCTOGENERAL(data);
    setTablaProducto(data);
    console.log(data);
    setLodingg(false);
});    
}
//:::::::::::::::::::::::::::::::::  FUNCION PARA BUSCADOR ::::::::::::::::::::::::::::::::::::::::::::::::::::::
const BuscarProductoGeneral = (e) =>
{
    let valor           = document.getElementById("BuscarInventarioPro").value;
    e.preventDefault();
    var codigo          = e.which || e.keyCode;
    if (codigo == 127){
    }
    if(codigo >= 32 && codigo <= 126)
    {
        const bandera   =   1;  
        console.log(String.fromCharCode(codigo));
        valor           = valor + String.fromCharCode(codigo);
    }
      document.getElementById("BuscarInventarioPro").value = valor
      filtrar(document.getElementById("BuscarInventarioPro").value);
}
//:::::::::::::::::::::::::::::::FUNCION FILTRAR::::::::::::::::::::::::::::::::::::::::::::::::::::::
const filtrar = (busqueda) => 
{
    const resultadoBusqueda = tablaproducto.filter((elementos) =>
    {
        if(elementos.codigo.toLowerCase().toString().includes(busqueda)||elementos.nombre.toLowerCase().toString().includes(busqueda)||elementos.descripcion.toLowerCase().toString().includes(busqueda))
        {
            return elementos;
        }
    });
    setBuscar(resultadoBusqueda);
    setPRODUCTOGENERAL(resultadoBusqueda);
}
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
//::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::
