//::::::::::::::: FUNCION PARA SELECCIONAR LOS DATOS DE LA TABLA ::::::::::::::::::::::::::::::::::://
const SELECTENVASE = () =>
{
    setLodingg(true);
    fetch(`${Constantes.RUTA_API}/obtenerenvase-conversionunidades.php`).then((res) =>
    res.json()).then((data)=>{
    setunidadMedidad(data);
    setTablaUnidad(data);
    //console.log(data);
    setLodingg(false);
});    
}
//::::::::::::::: FUNCION PARA ACTUALIZAR LOS DATOS DE LA TABLA ::::::::::::::::::::::::::::::::::::
const funcionactualizarconversiones = (e) =>
{
    e.preventDefault();
    //console.log(e.target[0].value); 
    const objetoenvioactualizarconversion =
    {
        id                          :      e.target[0].value,
        unidad_medidad_origen_id    :      e.target[1].value,
        unidad_medida_destino_id    :      e.target[2].value,
        factor                      :      e.target[3].value,
        fh_actualizar               :      e.target[4].value
    }
    const cargaUtil = JSON.stringify(objetoenvioactualizarconversion);
    //console.log(objetoenvioactualizarconversion);
    funcionUPDATECONVERSIONUNIDAD(cargaUtil);
};
const funcionUPDATECONVERSIONUNIDAD= async(cargaUtil) => 
{
    setLodinggModal(true);
    const respuesta =  await fetch(`${Constantes.RUTA_API}/actualizar-conversiones.php`, {
        method: "POST",
        body: cargaUtil,
    });
    setLodinggModal(false);
    //console.log("JSON RESPUESTA : "+respuesta);
    const exitoso = await   respuesta.json();
    if (exitoso) {
        // console.log("entre a notificacion");
        toast.warn('CONVERSION ACTUALIZADA ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    setActualizar(actualizar+1)
    }else
    {
        toast.error('NO SE REALIZO LA OPERACIÓN ', {
            position: "top-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
}
//:::::::::::::::::::::::::::::::::  FUNCION PARA BUSCADOR ::::::::::::::::::::::::::::::::::::::::::::::::::::::
const Buscar = (e) =>
{
    let valor           = document.getElementById("BuscarConversionUnidad").value;
    e.preventDefault();
    var codigo          = e.which || e.keyCode;
    if (codigo == 127){
    }
    if(codigo >= 32 && codigo <= 126)
    {
        const bandera   =   1;  
        //console.log(String.fromCharCode(codigo));
        valor           = valor + String.fromCharCode(codigo);
    }
    document.getElementById("BuscarConversionUnidad").value = valor
    filtrar(document.getElementById("BuscarConversionUnidad").value);
}
//:::::::::::::::::::::::::::::::FUNCION FILTRAR::::::::::::::::::::::::::::::::::::::::::::::::::::::
const filtrar = (busqueda) => 
{
    const resultadoBusqueda = tablaunidad.filter((elementos) =>
    {
        if(elementos.nombreunidadtable.toLowerCase().toString().includes(busqueda)||elementos.nombreunidadtable2.toLowerCase().toString().includes(busqueda))
        {
            return elementos;
        }
    });
    setBuscar(resultadoBusqueda);
    setunidadMedidad(resultadoBusqueda);
}
