import React,{useEffect,useState} from 'react'
import Navegacion from '../paginainicial/Navegador';
import PiedePagina from '../paginainicial/Piedepagina';
import PropTypes from 'prop-types'
import FormularioCompras from './FormularioCompras';
import TablaCompras from './TablaCompras';
import TablaComprasDetalle from './TablaComprasDetalle';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function BodyCompras(props) {

    const [banderalista,setLista]                           =   useState(false);
    //const [banderlistadetalle,setListaDetalle]              =   useState(false);
    //const [banderacombo,setCombo]                           =   useState(false);
    //const [banderacombodetalle,setCombodetalle]             =   useState(false);
    const [contadorefecto,setContadorEfecto]                =   useState(0);
     //::::::::::::::::::::::::: Funcion habilitar TABLA COMPRAS ::::::::::::::::::::::::
     const TABLACOMPRA= e => 
     {
         e.preventDefault();
         setLista(true);
         if(banderalista)
         {
             const compratabla                        = document.getElementById("IDCOMPRA");
             compratabla.style.display                = "";
             const compratabladetalle                 = document.getElementById("IDCOMPRARDETALLE");
             compratabladetalle.style.display         = "none";
             setLista(false);
         }
         setContadorEfecto(contadorefecto +1);
     }
      //::::::::::::::::::::::::: Funcion habilitar TABLA COMPRAS DETALLE ::::::::::::::::::::::::
    const TABLACOMPRADETALLE = e => 
    {
        e.preventDefault();
        setLista(true);
        if(banderalista)
        {
            const compratabla                         = document.getElementById("IDCOMPRA");
             compratabla.style.display                = "none";
             const compratabladetalle                 = document.getElementById("IDCOMPRARDETALLE");
             compratabladetalle.style.display         = "";
            setLista(false);
        }
        setContadorEfecto(contadorefecto +1);
    }
    useEffect(() => {
        if(contadorefecto == 0)
        {
            const compratabla                         = document.getElementById("IDCOMPRA");
             compratabla.style.display                = "";
             const compratabladetalle                 = document.getElementById("IDCOMPRARDETALLE");
             compratabladetalle.style.display         = "none";
        }
    }, [contadorefecto])
    return (
        <>
            <Navegacion/>

            <div className="container col-sm-12">
        <ToastContainer/>
            <ul className="nav justify-content-center texto-titulo  separacionmenusuperior">
            <li className="nav-item">
                <button className="btn colormenutablas btn-sm espaciomenusuperior"      onClick={TABLACOMPRA} title="TABLA PRODUCTOS GENERALES" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-receipt-cutoff" viewBox="0 0 16 16">
                <path d="M3 4.5a.5.5 0 0 1 .5-.5h6a.5.5 0 1 1 0 1h-6a.5.5 0 0 1-.5-.5zm0 2a.5.5 0 0 1 .5-.5h6a.5.5 0 1 1 0 1h-6a.5.5 0 0 1-.5-.5zm0 2a.5.5 0 0 1 .5-.5h6a.5.5 0 1 1 0 1h-6a.5.5 0 0 1-.5-.5zm0 2a.5.5 0 0 1 .5-.5h6a.5.5 0 0 1 0 1h-6a.5.5 0 0 1-.5-.5zm0 2a.5.5 0 0 1 .5-.5h6a.5.5 0 0 1 0 1h-6a.5.5 0 0 1-.5-.5zM11.5 4a.5.5 0 0 0 0 1h1a.5.5 0 0 0 0-1h-1zm0 2a.5.5 0 0 0 0 1h1a.5.5 0 0 0 0-1h-1zm0 2a.5.5 0 0 0 0 1h1a.5.5 0 0 0 0-1h-1zm0 2a.5.5 0 0 0 0 1h1a.5.5 0 0 0 0-1h-1zm0 2a.5.5 0 0 0 0 1h1a.5.5 0 0 0 0-1h-1z"/>
                <path d="M2.354.646a.5.5 0 0 0-.801.13l-.5 1A.5.5 0 0 0 1 2v13H.5a.5.5 0 0 0 0 1h15a.5.5 0 0 0 0-1H15V2a.5.5 0 0 0-.053-.224l-.5-1a.5.5 0 0 0-.8-.13L13 1.293l-.646-.647a.5.5 0 0 0-.708 0L11 1.293l-.646-.647a.5.5 0 0 0-.708 0L9 1.293 8.354.646a.5.5 0 0 0-.708 0L7 1.293 6.354.646a.5.5 0 0 0-.708 0L5 1.293 4.354.646a.5.5 0 0 0-.708 0L3 1.293 2.354.646zm-.217 1.198.51.51a.5.5 0 0 0 .707 0L4 1.707l.646.647a.5.5 0 0 0 .708 0L6 1.707l.646.647a.5.5 0 0 0 .708 0L8 1.707l.646.647a.5.5 0 0 0 .708 0L10 1.707l.646.647a.5.5 0 0 0 .708 0L12 1.707l.646.647a.5.5 0 0 0 .708 0l.509-.51.137.274V15H2V2.118l.137-.274z"/>
                </svg>  COMPRAS
                </button>
            </li>

           
               <li className="nav-item">
                <button className="btn colormenutablas btn-sm espaciomenusuperior"      onClick={TABLACOMPRADETALLE} title="TABLA PRODUCTOS GENERALES" >
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-calculator" viewBox="0 0 16 16">
                <path d="M12 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h8zM4 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h8a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H4z"/>
                <path d="M4 2.5a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-.5.5h-7a.5.5 0 0 1-.5-.5v-2zm0 4a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm0 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm0 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm3-6a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm0 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm0 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm3-6a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm0 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v4a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-4z"/>
                </svg> COSTEAR COMPRA
                </button>
            </li>
               
            <li className="nav-item">
                <a className="nav-link disabled"title="Otras Opciones">Disabled</a>
            </li>
            </ul>
            <div id="RPRODUCTO" className="row ">
            <div  className="col-sm-2 color-fondoMenu">
                <FormularioCompras/>
            </div>
            
            <div id="IDCOMPRA"  className="col-sm-10 ">
               <TablaCompras/>
            </div>
            <div id="IDCOMPRARDETALLE"  className="col-sm-10 ">
               <TablaComprasDetalle/>
            </div>

            </div>
        </div>
            <PiedePagina/>
        </>
    )
}

BodyCompras.propTypes = {

}

export default BodyCompras

